import React, { Component } from "react";
import { Sidenav, Nav, Dropdown } from "rsuite";
import { Navigation } from "react-minimal-side-navigation";
import "react-minimal-side-navigation/lib/ReactMinimalSideNavigation.css";
import { Icon } from "@iconify/react";
import { Redirect } from "react-router-dom";
import AuthService from "../../services/Authentification/authservices";
import cv from "../../assests/img/cv-update.png";
import rep from "../../assests/img/reponses.png";
import out from "../../assests/img/logout_icon.png";
import { Col, Row } from "react-bootstrap";
import { Container } from "react-bootstrap";
import LinkDuo from "../../utils/LinkDuo";
import { getCurrentUser } from "../../services/Authentification/oauthservices";
import Alert from "react-s-alert";
import { ACCESS_TOKEN } from "../../config/constants";
import Competence from "./Competence.js";
import Select from "react-select";
//import Select from "react-validation/build/select"

import Tabs from "../../pages/Cv/Tabs";
import "../../pages/Cv/Tab.css";
import CvServices from "../../services/CvServices/cvservices";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import { CheckPicker } from "rsuite";
import makeAnimated from "react-select/animated";

// import AuthVerify from "./common/auth-verify";
import EventBus from "./common/EventBus";
import { Loading } from "../../views/components/Loading";

const animatedComponents = makeAnimated();

export default class Profile extends Component {
  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);
    this.editEmployee = this.editEmployee.bind(this);
    this.setActiveTutorial = this.setActiveTutorial.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
    this.changelangueHandler = this.changelangueHandler.bind(this);
    this.changecompetenceHandler = this.changecompetenceHandler.bind(this);
    this.validerCompetence = this.validerCompetence.bind(this);
    this.validerExperience = this.validerExperience.bind(this);
    this.validereDUCATION = this.validereDUCATION.bind(this);
    this.validerCV = this.validerCV.bind(this);


    this.state = {
      loading: false,
      show: false,
      redirect: null,
      userReady: false,
      currentTutorial: null,
      currentIndex: -1,
      currentUser: { username: "" },
      id: null,
      nom: "",
      prenom: "",
      address: "",
      dateN: "",
      tel: "",
      users: [],
      agreement: false,
      langue: "",
      competence: "",
      nbannee: "",
      libelle: "",
      titreposte: "",
      description: "",
      etatp: "",
      dated: "",
      datef: "",
      niveau: "",
      diplome: "",
      universite: "",
      educations: [],
      experiences: [],
      competences: [],
      images: [],
langue_:""
      //value: 'langue',
    };
    // this.langue = [];

    // this.langue.push({ value: 'Franais', label: 'Franais' });
    // this.langue.push({ value: 'Anglais', label: 'Anglais' });
    // this.langue.push({ value: 'Arabe', label: 'Arabe' });
    // this.langue.push({ value: 'Allemand', label: 'Allemand' });
    // this.langue.push({ value: 'Espagnol', label: 'Espagnol' });
  }

  handleChange = (e) => this.setState({ agreement: e.target.checked });

  async componentDidMount() {
    await this.loadCurrentlyLoggedInUserDetails();
    window.location.reload();

  }

  async loadCurrentlyLoggedInUserDetails() {
    try {
      this.setState({
        loading: true,
      });

      this.setState({
        loading: false,
      });
    } catch (error) {
      this.setState({
        loading: false,
      });
    }

  }

  setActiveTutorial(tutorial, index) {
    this.setState({
      currentTutorial: tutorial,
      currentIndex: index,
    });
  }

  editEmployee(id) {
    this.props.history.push(`/user/${id}`);
  }

  componentDidMount() {
    const user = AuthService.getCurrentUser();
    getCurrentUser().then((response) => {
      localStorage.setItem("user", JSON.stringify(response));
      localStorage.setItem("ROLE_USER", JSON.stringify(response));


      this.setState({

        currentUser: response,
        authenticated: true,
        loading: false,

      });

    });
    if (user) {
      this.setState({
        currentUser: user,
        showModeratorBoard: user.roles?.includes("ROLE_CANDIDATURE"),
        //showAdminBoard: user.roles?.includes("ROLE_ENTREPRISE"),
      });
      this.setState({ user: user, userReady: true });
    }


    EventBus.on("logout", () => {
      this.handleLogout();
    });

    CvServices.getEducationByUser(user?.id).then((res) => {
      this.setState({ educations: res.data });
    });
    CvServices.getExperienceByUser(user?.id).then((res) => {
      this.setState({ experiences: res.data });
    });

    CvServices.getCompetenceByUser(user?.id).then((res) => {
      this.setState({ competences: res.data });
    });
  }

  getTutorial(id) {
    AuthService.getEmployeeById(id)
      .then((response) => {
        this.setState({
          currentTutorial: response.data,
        });
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }

  componentWillUnmount() {
    EventBus.remove("logout");
  }

  logOut() {
    AuthService.logout();
  }

  handleLogout() {
    localStorage.removeItem(ACCESS_TOKEN);
    localStorage.removeItem("user");
    localStorage.removeItem("ROLE_ENTREPRISE");

    this.setState({
      authenticated: false,
      currentUser: null,
    });
    Alert.success("You're safely logged out!");
  }
  /////////////////////////////////////Cv/////////////////////

  changeCvHandler = (event) => {
    this.setState({ images: event.target.files[0] });
  };

  validerCV(e) {
    e.preventDefault();
    alert("Merci d'ajouter vos Cv,notre tape est termin!");
    this.setState({
      message: "",
      successful: false,
    });

    //this.form.validateAll();
    const user = AuthService.getCurrentUser();
   


    CvServices.ValiderCv(user.id, this.state.images).then(
      (response) => {
        this.setState({
          message: response.data.message,
          successful: true,
        });
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
          successful: false,
          message: resMessage,
        });
      }
    );
  }

  ////////////cCompetence//////
  changelangueHandler (langue) {
    var test = langue[0].value
    if (langue.length >0){
   for ( let i = 1 ; i < langue.length ; i ++ ) {
test = test+","+langue[i].value
   }}
  console.log(langue)
    this.setState({ langue_:langue });
    this.setState({ langue:test });
  }

  changecompetenceHandler = (event) => {
    this.setState({ competence: event.target.value });
  };

  validerCompetence(e) {
    e.preventDefault();
    alert("Merci d'ajouter vos Competence, Passez  l'tape suivante!");
    this.setState({
      message: "",
      successful: false,
    });

    //this.form.validateAll();
    const user = AuthService.getCurrentUser();
    let InformationsProfessionnelles = {
      langue : this.state.langue,
      competence: this.state.competence,
    };
    console.log(InformationsProfessionnelles);
    console.log(
      "InformationsProfessionnelles => " +
        JSON.stringify(InformationsProfessionnelles)
    );

    CvServices.Validercomp(user.id, InformationsProfessionnelles).then(
      (response) => {
        this.setState({
          message: response.data.message,
          successful: true,
        });
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
          successful: false,
          message: resMessage,
        });
      }
    );
  }

  ///////////////////Experience//////////////////

  changeNbanneeHandler = (event) => {
    this.setState({ nbannee: event.target.value });
  };

  changeLibelleHandler = (event) => {
    this.setState({ libelle: event.target.value });
  };
  changeTitrePosteHandler = (event) => {
    this.setState({ titreposte: event.target.value });
  };

  changeDescriptionHandler = (event) => {
    this.setState({ description: event.target.value });
  };

  changeDateDHandler = (event) => {
    this.setState({ dated: event.target.value });
  };

  changeDateFinHandler = (event) => {
    this.setState({ datef: event.target.value });
  };

  validerExperience(e) {
    e.preventDefault();
    alert("Merci d'ajouter vos Expriences, Passez  l'tape suivante!");

    const user = AuthService.getCurrentUser();
    let Experience = {
      nbannee: this.state.nbannee,
      libelle: this.state.libelle,
      titreposte: this.state.titreposte,
      description: this.state.description,
      dated: this.state.dated,
      datef: this.state.datef,

      //etatp: this.state.etatp,
    };
    console.log("Experience => " + JSON.stringify(Experience));

    CvServices.ValiderExperience(user.id, Experience).then(
      (response) => {
        this.setState({
          message: response.data.message,
          successful: true,
        });
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
          successful: false,
          message: resMessage,
        });
      }
    );
  }
  /////////////////Education////////////////////

  changeNiveauHandler = (event) => {
    this.setState({ niveau: event.target.value });
  };

  changediplomeHandler = (event) => {
    this.setState({ diplome: event.target.value });
  };
  changeTitrePosteHandler = (event) => {
    this.setState({ titreposte: event.target.value });
  };

  changeUniversiteHandler = (event) => {
    this.setState({ universite: event.target.value });
  };

  validereDUCATION(e) {
    e.preventDefault();
    alert("Merci de valider vos Education, Passez  l'tape suivante!");
    const user = AuthService.getCurrentUser();
    console.log(user);
    let education = {
      niveau: this.state.niveau,
      diplome: this.state.diplome,
      universite: this.state.universite,
      dated: this.state.dated,
      datef: this.state.datef,

      //etatp: this.state.etatp,
    };
    console.log("Experience => " + JSON.stringify(education));

    CvServices.ValiderEducation(user.id, education).then(
      (response) => {
        this.setState({
          message: response.data.message,
          successful: true,
        });
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
          successful: false,
          message: resMessage,
        });
      }
    );
  }
  render() {
    if (this.state.redirect) {
      return <Redirect to={this.state.redirect} />;
    }

    const { currentUser } = this.state;
    const { educations, experiences, competences } = this.state;
    const options = [
      { value: "Franais", label: "Franais" },
      { value: "Anglais", label: "Anglais" },
      { value: "Arabe", label: "Arabe" },
      { value: "Allemand", label: "Allemand" },
      { value: "Espagnol", label: "Espagnol" },
      { value: "Italien", label: "Italien" },
    ];
    

    return (
      <div className="container">
        <div className="main-body">
          <nav aria-label="breadcrumb" className="main-breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item">
                <a href="index.html">Home</a>
              </li>
              <li className="breadcrumb-item">
                <a href="javascript:void(0)">User</a>
              </li>
              <li className="breadcrumb-item active" aria-current="page">
                User Profile
              </li>
            </ol>
          </nav>
          <div className="row gutters-sm">
            <div className="col-md-4 mb-3">
              <div className="card">
                <div className="card-body">
                  <div className="d-flex flex-column align-items-center text-center">
                    <img
                      //src={this.props.currentUser.imageUrl}

                      src={currentUser.images && currentUser.images[0]?.image}
                      className="rounded-circle"
                      width={150}
                    />
                    <div className="mt-3">
                      <h4>
                        Bienvenue <strong>{currentUser.username}</strong>
                      </h4>
                      {/* <h6>Upload a different photo...</h6>
                      <input
                        type="file"
                        class="text-center center-block file-upload"
                      /> */}

                      <p className="text-secondary mb-1">
                        Full Stack Developer
                      </p>

                      {/* 
                      <button
                        className="btn btn-primary"
                        onClick={this.showModal}
                      >
                        Comptence
                      </button> */}

                      {/* <Col>
                      <div className="col-md-4 moncomptelist">
                          <a href="mesreponses.html">
                            <img src={rep} />
                          </a>{" "}
                          <a href="/updateCandidature/${id}">Vos rponse</a>
                        </div>
                      </Col> */}
                      {/* <Col>
                        { <div className="col-md-4 moncomptelist">
                          <a href="/signIn">
                            <img
                              src={out}
                              onClick={this.handleLogout}
                              border="0"
                              alt="job tunisia, emploi tunisie, recrutement tunisie"
                            />
                          </a>{" "}
                          <a href="/signIn" onClick={this.handleLogout}>Dconnexion</a>

                        </div> }

                      </Col> */}
                    </div>
                    <div className="panel">
                      <div className="panel-heading">
                        <span className="panel-icon">
                          <i className="fa fa-trophy" />
                        </span>
                        <span className="panel-title"> My Skills</span>
                      </div>
                      <div>
                        {competences &&
                          competences.map((Info_Prof) => (
                            <div className="panel-body pb5">
                              <span className="label label-success mb-2">
                                {Info_Prof.competence}
                              </span>
                            </div>
                          ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div style={{ width: 240 }}></div>
              <Tabs>
                <div label="Comptence">
                  <div className="modal-content">
                    <div class="modal-header text-center pb-4">
                      <h3
                        class="modal-title w-100 white-text font-weight-bold"
                        id="myModalLabel"
                      >
                        <strong>COMPET</strong>
                        <a class="green-text font-weight-bold">
                          <strong>ANCE</strong>
                        </a>
                      </h3>
                    </div>

                    <div className="modal-body">
                      <div class="md-form mb-3">
                        {/* <input
                          type="text"
                          className="form-control"
                          value={this.state.langue}
                          onChange={this.changelangueHandler}
                        /> */}
                        <Select
                           value={this.state.langue_}
                           name="langue"
                           options={options}
                           onChange={this.changelangueHandler}
                           isMulti
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-email5"
                        >
                          Quelles langues connaissez-vous?
                        </label>
                      </div>

                      <div class="md-form pb-3">
                        <textarea
                          className="form-control"
                          rows="9"
                          value={this.state.competence}
                          onChange={this.changecompetenceHandler}
                        ></textarea>

                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          De quelles comptences, outils, technologies et
                          domaines d'expertise disposez-vous?
                        </label>
                      </div>
                    </div>

                    <div className="modal-footer">
                      <div id="competence">
                        <button
                          type="button"
                          className="btn btn-success"
                          onClick={this.validerCompetence}
                        >
                          Valider
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div label="Education">
                  <div className="modal-content">
                    <div class="modal-header text-center pb-4">
                      <h3
                        class="modal-title w-100 white-text font-weight-bold"
                        id="myModalLabel"
                      >
                        <strong>EDUCAT</strong>{" "}
                        <a class="green-text font-weight-bold">
                          <strong>ION</strong>
                        </a>
                      </h3>
                    </div>
                    <div className="modal-body">
                      <div class="md-form mb-3">
                        <select
                          className="form-control"
                          name="sexe"
                          id="sexe"
                          value={this.state.niveau}
                          onChange={this.changeNiveauHandler}
                          // validations={[required]}
                        >
                          <option value=""></option>
                          <option value="Ingenieur">Ingnieur</option>
                          <option value="Etudiant">Etudiant</option>
                          <option value="Licence">Licence</option>
                          <option value="Bac">Bac</option>
                        </select>
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-email5"
                        >
                          Quel est votre niveau d'tude actuel?
                        </label>
                      </div>

                      <div class="md-form pb-3">
                        <input
                          type="text"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value={this.state.diplome}
                          onChange={this.changediplomeHandler}
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          Diplme ou spcialit
                        </label>
                      </div>
                      <div class="md-form pb-3">
                        <input
                          type="text"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value={this.state.universite}
                          onChange={this.changeUniversiteHandler}
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          Universit ou tablissement
                        </label>
                      </div>
                      <div class="md-form mt-3">
                        <div className="col-sm-6">
                          <input
                            name="requested_order_ship_date"
                            type="date"
                            className="form-control"
                            value={this.state.dated}
                            onChange={this.changeDateDHandler}
                          />
                          <label
                            data-error="wrong"
                            data-success="right"
                            for="Form-pass5"
                          >
                            Date Dbut{" "}
                          </label>
                        </div>
                      </div>
                      <div class="md-form mt-3">
                        <div className="col-sm-6">
                          <input
                            name="requested_order_ship_date"
                            type="date"
                            className="form-control"
                            value={this.state.datef}
                            onChange={this.changeDateFinHandler}
                          />
                          <label
                            data-error="wrong"
                            data-success="right"
                            for="Form-pass5"
                          >
                            Date Fin{" "}
                          </label>
                        </div>
                      </div>
                    </div>
                    <div className="modal-footer">
                      <button
                        type="button"
                        className="btn btn-success"
                        onClick={this.validereDUCATION}
                      >
                        Valider
                      </button>
                    </div>
                  </div>
                </div>
                <div label="Exprience">
                  <div className="modal-content">
                    <div class="modal-header text-center pb-4">
                      <h3
                        class="modal-title w-100 white-text font-weight-bold"
                        id="myModalLabel"
                      >
                        <strong>EXPERI</strong>{" "}
                        <a class="green-text font-weight-bold">
                          <strong>ENCE</strong>
                        </a>
                      </h3>
                    </div>
                    <div className="modal-body">
                      <div class="md-form mb-3">
                        <input
                          type="number"
                          id="quantity"
                          name="quantity"
                          min="0"
                          max="15"
                          value={this.state.nbannee}
                          onChange={this.changeNbanneeHandler}
                        />

                        <label for="quantity">
                          Quel est le nombre d'annes de votre exprience?
                        </label>
                      </div>

                      <div class="md-form pb-3">
                        <input
                          type="text"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value={this.state.libelle}
                          onChange={this.changeLibelleHandler}
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          Nom de la dernire socit / Entreprise
                        </label>
                      </div>

                      <div class="md-form mb-3">
                        <input
                          type="text"
                          className="form-control"
                          value={this.state.titreposte}
                          onChange={this.changeTitrePosteHandler}
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-email5"
                        >
                          Titre de Poste - Mission
                        </label>
                      </div>
                      <div class="md-form mb-3">
                        <textarea
                          className="form-control"
                          rows="9"
                          value={this.state.description}
                          onChange={this.changeDescriptionHandler}
                        ></textarea>
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-email5"
                        >
                          Description
                        </label>
                      </div>
                      <div class="md-form mt-3">
                        <div className="col-sm-6">
                          <input
                            name="requested_order_ship_date"
                            type="date"
                            className="form-control"
                            value={this.state.dated}
                            onChange={this.changeDateDHandler}
                          />
                          <label
                            data-error="wrong"
                            data-success="right"
                            for="Form-pass5"
                          >
                            Date Dbut
                          </label>
                        </div>
                      </div>
                      <div class="md-form mt-3">
                        <div className="col-sm-6">
                          <input
                            name="requested_order_ship_date"
                            type="date"
                            className="form-control"
                            value={this.state.datef}
                            onChange={this.changeDateFinHandler}
                          />
                          <label
                            data-error="wrong"
                            data-success="right"
                            for="Form-pass5"
                          >
                            Date Fin
                          </label>
                        </div>
                      </div>
                    </div>
                    <div className="modal-footer">
                      <div id="competence">
                        <button
                          type="button"
                          className="btn btn-success"
                          onClick={this.validerExperience}
                        >
                          Valider
                        </button>
                      </div>
                    </div>
                  </div>
                </div>

                <div label="Cv">
                  <div className="modal-content">
                    <div class="modal-header text-center pb-4">
                      <h3
                        class="modal-title w-100 white-text font-weight-bold"
                        id="myModalLabel"
                      >
                        <a class="green-text font-weight-bold">
                          <strong>CV</strong>
                        </a>
                      </h3>
                    </div>
                    <div className="modal-body">
                      <label for="myfile">Select a file:</label>
                      <input type="file" id="myfile" name="myfile" 
                       accept="application/pdf" 
                       onChange={this.changeCvHandler}/>
                    </div>
                    <div className="modal-footer">
                      <div id="competence">
                        <button type="button" className="btn btn-success"
                                                  onClick={this.validerCV}
                                                  >
                          Valider
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </Tabs>
              <h4 className="header-title">Langue(s)</h4>
              {competences &&
                competences.map((Info_Prof) => 
                 ( Info_Prof.langue.split(',').map(x=>
              
                  <div className="card-box">
                      
                    <div className="pt-1">
                      <h6 className="text-uppercase mt-0">
                        {x} <span className="float-right"></span>
                      </h6>
                      <div className="progress progress-sm m-0">
                        <div
                          className="progress-bar bg-purple"
                          role="progressbar"
                          aria-valuenow={80}
                          aria-valuemin={0}
                          aria-valuemax={100}
                          style={{ width: "90%" }}
                        >
                          <span className="sr-only">90% Complete</span>
                        </div>
                      </div>
                    </div>
                  </div>)
                ))}
            </div>

            <div className="col-md-8">
              <div className="card mb-3">
                <div className="card-body">
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Full Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {currentUser.firstName} {currentUser.lastName}
                    </div>
                  </div>
                  <hr />
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Email</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {currentUser.email}
                    </div>
                  </div>
                  <hr />
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">DateN</h6>
                    </div>
                    <div className="col-sm-9 text-secondary" type="date">
                      {/* <input  
                         name="requested_order_ship_date" 
                         type="date" 
                        
                         /> */}
                      {currentUser.dateN}
                    </div>
                  </div>
                  <hr />
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Mobile</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {currentUser.tel}
                    </div>
                  </div>
                  <hr />
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Address</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {currentUser.address}
                    </div>
                  </div>
                  <hr />
                  <div className="row">
                    <LinkDuo to={"/user/" + currentUser.id}>
                      <div className="col-sm-12">
                        <a
                          className="btn btn-secondary "
                          target="__blank"
                          href="https://www.bootdey.com/snippets/view/profile-edit-data-and-skills"
                        >
                          Edit
                        </a>
                      </div>
                    </LinkDuo>
                    <div className="col-sm-12"></div>
                    <LinkDuo  className="btn btn-success "
                      target="__blank" to="/strides/Suivie-Reponse">Suivi de mes rponses </LinkDuo>

                    {/* <a
                      className="btn btn-success "
                      target="__blank"
                      href="/strides/Suivie-Reponse"
                    >
                      Suivi de mes rponses
                    </a> */}
                  </div>
                </div>
              </div>
              <div className="row gutters-sm">
                <div className="col-sm-6 mb-3">
                  <div className="panel">
                    <div className="panel-heading">
                      <span className="panel-icon">
                        <i className="fa fa-pencil" />
                      </span>
                      <span className="panel-title">About Me</span>
                    </div>
                    <div className="panel-body pb5">
                      <h6>Experience</h6>
                      <div>
                        {experiences &&
                          experiences.map((experience) => (
                            <div>
                              <h4>Poste : {experience.titreposte}</h4>
                              <p className="text-muted">
                                {" "}
                                Etablissement : {experience.libelle}, Nbre
                                d'experience : {experience.nbannee}
                                <br /> description : {experience.description},
                                Date Dbut :{experience.dated} - Date Fin :{" "}
                                {experience.datef}
                              </p>
                            </div>
                          ))}
                      </div>
                      <hr className="short br-lighter" />

                      <h6>Education</h6>
                      <div>
                        {educations &&
                          educations.map((education) => (
                            <div>
                              <h4>Etudes et Formations : {education.niveau}</h4>
                              <p className="text-muted">
                                <strong>Diplme : </strong> {education.diplome}
                                <br /> {education.universite}, {education.dated}{" "}
                                - {education.datef}
                              </p>
                            </div>
                          ))}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
