import React, { Component } from 'react';
import InputProfile from "./InputProfile";
import TextAreaInput from "./TextAreaInput";
import  action from "../../services/offreservices/actionoffre";
import Modal from './Modal.js';
import LinkDuo from "../../utils/LinkDuo"
import AuthService from "../../services/Authentification/authservices";
import { Toaster, toast } from 'react-hot-toast'
import EventBus from "../../components/Authentification/common/EventBus";
import { getCurrentUser } from "../../services/Authentification/oauthservices";




export default class resume extends Component {

    constructor(props) {
        super(props)
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
        this.ValiderCandidature = this.ValiderCandidature.bind(this);

        


        this.state = {
          loading: false,
      show: false,
            id: this.props.match.params.id,
            
            offre: {},
            motivation:"",
            status:"",
            cv:"",
            images: [],
            currentUser: [] ,

            

        }
     
    }

    showModal = () => {
      if (localStorage.getItem("user")){
      this.setState({ show: true });
      }
      else{
        this.props.history.push(`/signIn`);
      }
    };
  
    hideModal = () => {
      this.setState({ show: false });
    };
  
    openModal() {
      this.setState((prevState) => ({ show: !prevState.show }));
    }
    closeModal(e) {
      if (e.target.id === "modal") {
        this.setState({ show: false });
      }
    }

    componentDidMount(){
      const user = AuthService.getCurrentUser();
      getCurrentUser().then((response) => {
        this.setState({
          currentUser: response,
          authenticated: true,
          loading: false,
        });
      });
      if (user) {
        this.setState({
          currentUser: user,
          showAdminBoard: user.roles.includes("ROLE_ENTREPRISE"),
        });
        this.setState({ user: user, userReady: true });
      }
  
    
        action.getOffreById(this.state.id).then( res => {
            this.setState({offre: res.data});
        })
    }
    ////////////////////////////candidature/////////////////

    ValiderCandidature(e) {
      e.preventDefault();
     alert("Votre candidature est bien envoyer");
      
     const user = AuthService.getCurrentUser();
     const id = this.props.match.params.id;
      //console.log(offre);
      let candidature = {
        motivation: this.state.motivation,
        
        
        //etatp: this.state.etatp,
  
  
      };
      console.log(
        "candidature => " +
          JSON.stringify(candidature)
      );
  
      action.EnvoyerCandidature(user.id, id, candidature, this.state.images).then(
        (response) => {
          this.setState({
            message: response.data.message,
            successful: true,
          });
          toast.success('Candidature bien Envoy!')
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
  
          this.setState({
            successful: false,
            message: resMessage,
          });
        }
      );
    }


    changeMotivationHandler = (event) => {
      this.setState({ motivation: event.target.value });
    };

    changeCvHandler = (event) => {
      this.setState({ images: event.target.files[0] });
    };

    render() {

      const { currentUser } = this.state;
  

        return (
            
         <div className="container">
           <Modal show={this.state.show} handleClose={this.hideModal}>
            <div className="modal-content">
              <div class="modal-header text-center pb-4">
                <h3
                  class="modal-title w-100 white-text font-weight-bold"
                  id="myModalLabel"
                >
                  <strong>POSTUL</strong>{" "}
                  <a class="green-text font-weight-bold">
                    <strong>ER</strong>
                  </a>
                </h3>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  {this.state.show && (
                    <span
                      aria-hidden="true"
                      id="modal"
                      onClick={(e) => this.closeModal(e)}
                    >
                      
                    </span>
                  )}
                </button>
              </div>
              <div className="modal-body">
                <div class="md-form mb-3">
                <input
                          type="text"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value = {currentUser.firstName} 
                        />
              
                  <label
                    data-error="wrong"
                    data-success="right"
                    for="Form-email5"
                  >
                   Votre Nom
                  </label>
                </div>

                <div class="md-form pb-3">
                        <input
                          type="text"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value = {currentUser.lastName} 
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          Votre Prenom
                        </label>
                      </div>

                      <div class="md-form pb-3">
                        <input
                          type="email"
                          id="Form-pass5"
                          class="form-control validate white-text"
                          value = {currentUser.email} 
                        />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"
                        >
                          Votre email
                        </label>
                      </div>

                      <div class="md-form pb-3">
                      <input type="file"
                       name="upload" 
                       accept="application/pdf" 
                       onChange={this.changeCvHandler}
                       />
                        <label
                          data-error="wrong"
                          data-success="right"
                          for="Form-pass5"

                        >
                          Tlcharger votre CV
                        </label>
                      </div>

                <div class="md-form pb-3">
                <textarea
                          className="form-control"
                          rows="9"
                          value={this.state.motivation}
                          onChange={this.changeMotivationHandler}
                        ></textarea>
                  <label
                    data-error="wrong"
                    data-success="right"
                    for="Form-pass5"
                  >
                    Lettre de motivation
                  </label>
                </div>
           
               
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-dark"
                  onClick={() => this.openModal()}
                >
                  Close
                </button>
                {this.state.show && (
                  <div id="modal" onClick={(e) => this.closeModal(e)}>
                    <button type="button" className="btn btn-primary"
                    onClick={this.ValiderCandidature}>
                      Envoyer
                    </button>
                  </div>
                )}
              </div>
            </div>
          </Modal>
          <Toaster
  position="top-right"
  reverseOrder={false}
/>
  <div className="card">
    <div className="card-body">
      
      <div className="row">
        <div className="col-lg-4 col-md-5 col-sm-6">
          <div className="white-box text-center"><img src={this.state.offre.images && this.state.offre.images[0]?.image} className="img-responsive" /></div>
        </div>
        <div className="col-lg-7 col-md-7 col-sm-6">
          <h3 className="card-title"><strong>{this.state.offre.title}</strong></h3>
          <h4 className="box-title mt-5">description :</h4>
          <p>{this.state.offre.description}</p>
          <h2 className="mt-5">
           {this.state.offre.nbreposte}<small className="text-success">postes</small>
          </h2>
         
          <h3 className="box-title mt-5">Mots Cls :</h3>
         
          <div>
          <ul className="list-unstyled">
            <li><i className="fa fa-check text-success" /><strong>{this.state.offre.motcle}</strong></li>
      
          </ul>
          </div>
         
        </div>
        <button className="btn btn-dark btn-rounded mr-1" data-toggle="tooltip" title data-original-title="Add to cart">
        <LinkDuo to="/strides/offres"> Retour<i className="fa fa-adress-book" /></LinkDuo>
          </button>
          <button className="btn btn-primary btn-rounded" onClick={this.showModal}>Postuler</button>
        <div className="col-lg-12 col-md-12 col-sm-12">
          <h3 className="box-title mt-5">Information Gnrale</h3>
          <div className="table-responsive">
            <table className="table table-striped table-product">
              <tbody>
                <tr>
                  <td width={390}>Type d'emploi dsir :</td>
                  <td>{this.state.offre.type}</td>
                </tr>
                <tr>
                  <td>Rnumration :</td>
                  <td>{this.state.offre.renumeration}</td>
                </tr>
                <tr>
                  <td>Niveau d'tude :</td>
                  <td>{this.state.offre.niveau}</td>
                </tr>
                <tr>
                  <td>Langue :</td>
                  <td>{this.state.offre.langue}</td>
                </tr>
                <tr>
                  <td>Experience :</td>
                  <td>{this.state.offre.experience}</td>
                </tr>
                <tr>
                  <td>Date d'expiration :</td>
                  <td>{this.state.offre.dateExpiration}</td>
                </tr>
                <tr>
                  <td>Genre:</td>
                  <td>{this.state.offre.genre}</td>
                </tr>
                <tr>
                  <td>Address</td>
                  <td>{this.state.offre.adresse}</td>
                </tr>
                <tr>
                  <td>Catgorie:</td>
                  <td>{this.state.offre.categorie}</td>
                </tr>
               
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>

        )
    }
}
