import React, { Component, useState } from "react";

import AvFeedback from "availity-reactstrap-validation/lib/AvFeedback";
import AvForm from "availity-reactstrap-validation/lib/AvForm";
import AvGroup from "availity-reactstrap-validation/lib/AvGroup";
import AvInput from "availity-reactstrap-validation/lib/AvInput";
import { stateToHTML } from "draft-js-export-html";
import {Editor, EditorState} from 'draft-js';
import { useHistory } from "react-router-dom";
import RUG, { DropArea } from "react-upload-gallery";
import Button from "reactstrap/lib/Button";
import Card from "reactstrap/lib/Card";
import CardBody from "reactstrap/lib/CardBody";
import CardFooter from "reactstrap/lib/CardFooter";
import CardHeader from "reactstrap/lib/CardHeader";
import Col from "reactstrap/lib/Col";
import CustomInput from "reactstrap/lib/CustomInput";
import FormGroup from "reactstrap/lib/FormGroup";
import FormText from "reactstrap/lib/FormText";
import Input from "reactstrap/lib/Input";
import Label from "reactstrap/lib/Label";
import Row from "reactstrap/lib/Row";
import slugify from "slugify";
import { AvButton } from "../../views/components/AvButton";
import { CustomOptions } from "../../views/components/custom";
import { RichEditor } from "../../views/components/RichEditor";
import { eventTypes } from "../../enums/entities.enum";
import {
  IMAGE_NOT_EMPTY,
  TITLE_LENGTH,
  TITLE_NOT_EMPTY,
  TITLE_UNIQUE,
  TYPE_NOT_EMPTY,
  TYPE_NOT_ENUM,
  VALIDATION_ERROR,
} from "../../enums/errors.enum";
import Moment from "react-moment";
import moment from 'moment';
import Select from "react-select";
import addMonths from 'date-fns/addMonths';
import format from 'date-fns/format';
import LinkDuo from "../../utils/LinkDuo"
import action from "../../services/offreservices/actionoffre";
import AuthService from "../../services/Authentification/authservices";
import { Toaster, toast } from 'react-hot-toast'




export default class AddOffre extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      title: this.props.title,
      title: "",
      description: "",
      adresse: "",
      type: "",

      nbreposte: "1",
      dateAjout:"",
      dateExpiration: "",
      contenu: "",
      experience: "",
      langue:"",
      niveau:"",
      renumeration:"",
      genre:"",
      categorie:"",
      motcle:"",
      

      images: [],
      langue_:""
    };

    this.saveOrUpdateEmployee = this.saveOrUpdateEmployee.bind(this);
    this.changelangueHandler = this.changelangueHandler.bind(this);

  }

  saveOrUpdateEmployee = (e) => {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false,
    });
    const user = AuthService.getCurrentUser();

    let offre = {
      title: this.state.title,
      description: this.state.description,
      adresse: this.state.adresse,
      type: this.state.type,
      nbreposte: this.state.nbreposte,
      dateAjout: this.state.dateAjout,
      dateExpiration: this.state.dateExpiration,
      contenu: this.state.contenu,
      experience: this.state.experience,
      langue: this.state.langue,
      niveau: this.state.niveau,
      renumeration: this.state.renumeration,
      genre: this.state.genre,
      categorie: this.state.categorie,
      motcle: this.state.motcle,


    };
    console.log("offre => " + JSON.stringify(offre));

    action.createProduct(user.id, offre, this.state.images).then(
      (response) => {
        this.setState({
          message: response.data.message,
          successful: true,
        });
        toast.success('Settings saved!')
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        this.setState({
          successful: false,
          message: resMessage,
        });
      }
    );
  };

  changeFirstNameHandler = (event) => {
    this.setState({ title: event.target.value });
  };

  changeLastNameHandler = (event) => {
    this.setState({ description: event.target.value });
  };

  changeAdresseHandler = (event) => {
    this.setState({ adresse: event.target.value });
  };
  changeContenuHandler = (event) => {
    this.setState({ contenu: event.target.value });
  };
  changeNombrePosteHandler = (event) => {
    this.setState({ nbreposte: event.target.value });
  };
  changeTypeHandler = (event) => {
    this.setState({ type: event.target.value });
  };
  changeExpirationHandler = (event) => {
    this.setState({ dateExpiration: event.target.value });
  };
  changeExperienceHandler = (event) => {
    this.setState({ experience: event.target.value });
  };
  changelangueHandler (langue) {
    var test = langue[0].value
    if (langue.length >0){
   for ( let i = 1 ; i < langue.length ; i ++ ) {
test = test+","+langue[i].value
   }}
  console.log(langue)
    this.setState({ langue_:langue });
    this.setState({ langue:test });
  }
  changeNiveauHandler = (event) => {
    this.setState({ niveau: event.target.value });
  };
  changeExperienceHandler = (event) => {
    this.setState({ experience: event.target.value });
  };
  changeRenumerationHandler = (event) => {
    this.setState({ renumeration: event.target.value });
  };
  changeGenreHandler = (event) => {
    this.setState({ genre: event.target.value });
  };
  changeCategorieHandler = (event) => {
    this.setState({ categorie: event.target.value });
  };
  changeMotCleHandler = (event) => {
    this.setState({ motcle: event.target.value });
  };
  changeImagesHandler = (event) => {
    this.setState({ images: event.target.files[0] });
  };
  cancel() {
    //this.props.history.push('/offres');
  }

  render() {

    const options = [
      { value: "Franais", label: "Franais" },
      { value: "Anglais", label: "Anglais" },
      { value: "Arabe", label: "Arabe" },
      { value: "Allemand", label: "Allemand" },
      { value: "Espagnol", label: "Espagnol" },
      { value: "Italien", label: "Italien" },
      { value: "Japonais", label: "Japonais" },
      { value: "Espagnol", label: "Espagnol" },
      { value: "Russe", label: "Russe" },
      { value: "Turc", label: "Turc" },

    ];
    const maxDate = format(addMonths(new Date(), 1), "yyyy-MM-dd");

    var today = new Date();
var dd = today.getDate();
var mm = today.getMonth() + 1; //January is 0 so need to add 1 to make it 1!
var yyyy = today.getFullYear();

if (dd < 10) {
    dd = '0' + dd
}

if (mm < 10) {
    mm = '0' + mm
}

today = yyyy + '-' + mm + '-' + dd;
var minDate = "2022-02-15"
  
    return (
      <div class="container">
        <ol class="breadcrumb">
          <li>
            <a href="index.html">Home</a>
          </li>
          <li class="active">Publication</li>
        </ol>
        <Card>
          <CardHeader>
            <strong>Offre : </strong> Ajouter
          </CardHeader>
          <CardBody>
            <AvForm>
              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Titre</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                    <AvInput
                      required
                      type="text"
                      name="title"
                      value={this.state.title}
                      // validate={{ nameIsUnique }}
                      onChange={this.changeFirstNameHandler}
                      placeholder="Titre..."
                    />
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>

                  <FormText color="muted">Titre de l'offre  ajouter</FormText>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Nombre de postes</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                    <AvInput
                      required
                      type="number"
                      id="quantity"
                      name="quantity"
                      min="1"
                      max="15"
                      placeholder="Nombre de postes vacants ..."
                      value={this.state.nbreposte}
                      onChange={this.changeNombrePosteHandler}
                    />
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Adresse de l'offre</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                    <AvInput
                      required
                      type="text"
                      name="adresse"
                      value={this.state.adresse}
                      onChange={this.changeAdresseHandler}
                      placeholder="Adresse ..."
                    />
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="textarea-input">Description</Label>
                </Col>
                <Col xs="12" md="9">
                  <Input
                  required
                    type="textarea"
                    name="description"
                    value={this.state.description}
                    onChange={this.changeLastNameHandler}
                    placeholder="Description..."
                    
                  />
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Type</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="sexe"
                              id="sexe"
                              value={this.state.type}
                              onChange={this.changeTypeHandler}
                             // validations={[required]}
                             placeholder="Type d'emploi dsir..."

                              >
                              <option value="">Veuillez choisir le type</option>
                              <option value= "CDD">CDD</option>
                              <option value="CDI">CDI</option>
                              <option value="Temps plein">Temps plein</option>
                              <option value="Temps partiel">Temps partiel</option>
                              <option value="Frelance/Independant">Frelance/Independant</option>
                              <option value="Saisonnier">Saisonnier</option>
                              <option value="Contrat El Karma">Contrat El Karma</option>
                              <option value="SIVP">SIVP</option>

                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Contenu</Label>
                </Col>
                <Col xs="12" md="9">
                  <RichEditor
                    value={this.state.contenu}
                    onStateChanged={this.changeContenuHandler}
                  />
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Catgorie*</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="categorie"
                              id="categorie"
                              value={this.state.categorie}
                              onChange={this.changeCategorieHandler}
                             // validations={[required]}
                            

                              >
                              <option value="">cliquer pour slectionner</option>
                              <option value= "Informatique">Informatique</option>
                              <option value="Commerce">Commerce</option>
                              <option value="Stage">Stage</option>
                              <option value="Technologie de l'information">Technologie de l'information</option>
                              <option value="Transport">Transport</option>
                              <option value="Textile">Textile</option>
                              <option value="Tlcommunication">Tlcommunication</option>
                              <option value="Industrie">Industrie</option>
                              <option value="Electronique">Electronique</option>
                              <option value="Ingnierie">Ingnierie</option>
                              <option value="Tourisme">Tourisme</option>
                              <option value="Sport et fitness">Sport et fitness</option>



                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">date d'expiration</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                    <AvInput
                      required
                      type="date"
                      name="requested_order_ship_date"
                      min={today} max={maxDate}
                      //minDate= "today"
                      //maxDate= {moment().subtract(7, 'days')} 
                      value={this.state.dateExpiration}
                      onChange={this.changeExpirationHandler}
                    />
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Experience</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="experience"
                              id="experience"
                              value={this.state.experience}
                              onChange={this.changeExperienceHandler}
                             // validations={[required]}
                            

                              >
                              <option value="">Slectionnez Experience</option>
                              <option value= "Dbutant">Dbutant</option>
                              <option value="0  1 an">0  1 an</option>
                              <option value="1  3 ans">1  3 ans</option>
                              <option value="3  5 ans">3  5 ans</option>
                              <option value="5  10 ans">5  10 ans</option>
                              <option value="Plus 10 ans">Plus 10 ans</option>
                          

                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Niveau d'tude</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="experience"
                              id="experience"
                              value={this.state.niveau}
                              onChange={this.changeNiveauHandler}
                             // validations={[required]}
                            

                              >
                              <option value="">Slectionnez niveau d'tude</option>
                              <option value= "Diplome non valid">Diplome non valid</option>
                              <option value="Bac Professionnel">Bac Professionnel</option>
                              <option value="Xpert, Recherche">Xpert, Recherche</option>
                              <option value="Doctorat">Doctorat</option>
                              <option value="Ingnieur">Ingnieur</option>
                              <option value="Licence, Bac+3">Saisonnier</option>
                              <option value="Bac non valid">Bac non valid</option>
                              <option value="Etudiant">Etudiant</option>
                              <option value="Master, Bac+5">Master, Bac+5</option>


                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Rnumration propose</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="experience"
                              id="experience"
                              value={this.state.renumeration}
                              onChange={this.changeRenumerationHandler}
                             // validations={[required]}
                            

                              >
                              <option value="">Slectionnez rnumration propose</option>
                              <option value= "Infrieur  500 DT">Infrieur  500 DT</option>
                              <option value="Entre 500 DT et 1000DT">Entre 500 DT et 1000DT</option>
                              <option value="Entre 1000 DT et 1500 DT">Entre 1000 DT et 1500 DT</option>
                              <option value="Entre 1500 DT et 2000 DT">Entre 1500 DT et 2000 DT</option>
                              <option value="Entre 2000 DT et 3000 DT">Entre 2000 DT et 3000 DT</option>
                              <option value="Plus 3000 DT">Plus 3000 DT</option>
                              <option value="Confidentiel">Confidentiel</option>

                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>
              <div className="item">
                  <h5>Date Enregistrement :</h5>
                  <p className="mb-3">
                    <Moment locale="fr" date={this.state.dateAjout} format=" dddd, Do MMMM  YYYY  hh:mm" />
                  </p>
                </div>
                <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Langue</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                  <Select
                           value={this.state.langue_}
                           name="langue"
                           options={options}
                          onChange={this.changelangueHandler}
                           isMulti
                        />
                        
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Genre</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                
                    <select
                    required
                              className="form-control"
                              name="Genre"
                              id="Genre"
                              value={this.state.genre}
                              onChange={this.changeGenreHandler}
                             // validations={[required]}
                            

                              >
                              <option value="">Slectionnez Genre</option>
                              <option value= "Fminim">Fminim</option>
                              <option value="Masculin">Masculin</option>

                            </select>
                    
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Mot Cls</Label>
                </Col>
                <Col xs="12" md="9">
                  <AvGroup>
                    <AvInput
                      required
                      type="text"
                      name="mot"
                      value={this.state.motcle}
                      // validate={{ nameIsUnique }}
                      onChange={this.changeMotCleHandler}
                      placeholder="#mot cls..."
                    />
                    <AvFeedback className="text-capitalize"></AvFeedback>
                  </AvGroup>

                  <FormText color="muted">mot cls de l'offre  ajouter</FormText>
                </Col>
              </FormGroup>

              <FormGroup row>
                <Col md="2">
                  <Label htmlFor="text-input">Image de l'offre</Label>
                </Col>
                <Col xs="12" md="9">
                  <CustomInput
                    id="cover"
                    name="cover"
                    type="file"
                    accept="image/png, image/jpeg, image/jpg"
                    onChange={this.changeImagesHandler}
                  />
                </Col>
              </FormGroup>
              <Toaster
  position="top-right"
  reverseOrder={false}
/>
              <CardFooter>
                <center>
                  <Button
                    type="submit"
                    size="lg"
                    color="success"
                    className="btn btn-success"
                    onClick={this.saveOrUpdateEmployee}
                  >
                    Save
                  </Button>

                  <Button
                    type="reset"
                    size="lg"
                    color="dark"
                    // onClick={handleCancel}
                  >
                    <LinkDuo to="/strides/Profile-Client"> <i className="fa fa-ban"></i> Annuler </LinkDuo>

                    {/* <a href="/strides/Profile-Client" >
                    <i className="fa fa-ban"></i> Annuler
                    </a> */}
                  </Button>
                </center>
              </CardFooter>
            </AvForm>
          </CardBody>
        </Card>
      </div>
    );
  }
}
