import React, { Component, useState } from "react";
import Recaptcha from "react-recaptcha";
import { isEmail } from "validator";
import { Container, Row, Col } from "react-bootstrap";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import contacts from "../services/contactservices/contacts";

const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

const email = (value) => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const vname = (value) => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The firstName must be between 3 and 20 characters.
      </div>
    );
  }
};

export default class Contact extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // step 2
      nom: this.props.nom,
      nom: "",
      num_tel: "",
      description: "",
      email: "",
      isVerified: false,
    };

    this.handleSubscribe = this.handleSubscribe.bind(this);
    this.recaptchaLoaded = this.recaptchaLoaded.bind(this);
    this.verifyCallback = this.verifyCallback.bind(this);
    this.saveOrUpdateEmployee = this.saveOrUpdateEmployee.bind(this);
  }

  /* saveOrUpdateEmployee = (e) => {
    e.preventDefault();
    this.setState({
      message: "",
      successful: false
    });
    let Contact = {
      nom: this.state.nom,
      num_tel: this.state.num_tel,
      email: this.state.email,
      description: this.state.description,
      
    };
    console.log("Contact => " + JSON.stringify(Contact));
    contacts.Envoyer(Contact);
    this.handleSubscribe();
  }; */
  saveOrUpdateEmployee(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false
    });

    this.form.validateAll();

    let Contact = {
      nom: this.state.nom,
      num_tel: this.state.num_tel,
      email: this.state.email,
      description: this.state.description,
      
    };
    console.log("Contact => " + JSON.stringify(Contact));


    if (this.checkBtn.context._errors.length === 0) {
      contacts.Envoyer(
        
        Contact,
        this.handleSubscribe()
        
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage
          });
        }
      );
    }
  }

  changenomHandler = (event) => {
    this.setState({ nom: event.target.value });
  };

  changenum_telHandler = (event) => {
    this.setState({ num_tel: event.target.value });
  };
  changeEmailHandler = (event) => {
    this.setState({ email: event.target.value });
  };
  changeDescriptionlHandler = (event) => {
    this.setState({ description: event.target.value });
  };

  recaptchaLoaded() {
    console.log("capcha successfully loaded");
  }

  handleSubscribe() {
    if (this.state.isVerified) {
       alert("votre message est bien reu!");
    } else {
      alert("Please verify that you are a human!");
    }
  }

  verifyCallback(response) {
    if (response) {
      this.setState({
        isVerified: true,
      });
    }
  }

  render() {
    return (
      <>
        <section id="contact" classNameName="contact-area">
          <Container>
            <Row>
              <Col>
                <div className="row">
                  <div className="col-lg-5 d-flex align-items-stretch">
                    <div className="info">
                      <div className="email">
                        <i className="bi bi-envelope"></i>
                        <h4>Email:</h4>
                        <p>gbouricha@strideshumancapital.com</p>
                      </div>

                      <div className="phone">
                        <i className="bi bi-phone"></i>
                        <h4>Tlphone:</h4>
                        <p>+216 22 817 801</p>
                      </div>
                    </div>
                  </div>
                </div>
              </Col>
              <Col>
                <Form
                  onSubmit={this.saveOrUpdateEmployee}
                  ref={(c) => {
                    this.form = c;
                  }}
                >
                  <div className="row">
                    <div className="col-sm-4">
                      <Input
                        className="form-control"
                        type="text"
                        placeholder="Name"
                        autoComplete="off"
                        value={this.state.nom}
                        onChange={this.changenomHandler}
                        validations={[required, vname]}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Input
                        className="form-control"
                        type="text"
                        placeholder="Email"
                        value={this.state.email}
                        onChange={this.changeEmailHandler}
                        validations={[required, email]}
                      />
                    </div>
                    <div className="col-sm-4">
                      <Input
                        className="form-control"
                        type="text"
                        placeholder="Phone"
                        value={this.state.num_tel}
                        onChange={this.changenum_telHandler}
                        validations={[required]}
                      />
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-sm-12">
                      <textarea
                        placeholder="Type your message here..."
                        className="form-control"
                        rows="9"
                        value={this.state.description}
                        onChange={this.changeDescriptionlHandler}
                        validations={[required]}
                      ></textarea>
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-sm-6">
                      <label className="checkbox">
                        <input type="checkbox" /> En soumettant ce formulaire,
                        j'accepte que les informations saisies soient exploites
                        dans le cadre de la demande et de la relation
                        commerciale qui peut en dcouler
                      </label>
                    </div>
                    <div className="col-sm-6 text-right">
                      <Recaptcha
                        sitekey="6LfwGdobAAAAAJo9-Ml14hmQEwbDs-YD2BeAw5FH"
                        render="explicit"
                        onloadCallback={this.recaptchaLoaded}
                        verifyCallback={this.verifyCallback}
                      />

                      <button className="btn btn-action" type="submit">
                        Send messaage
                      </button>
                    </div>
                  </div>

                  {this.state.message && (
                    <div className="form-group">
                      <div
                        className={
                          this.state.successful
                            ? "alert alert-success"
                            : "alert alert-danger"
                        }
                        role="alert"
                      >
                        {this.state.message}
                      </div>
                    </div>
                  )}
                  <CheckButton
                    style={{ display: "none" }}
                    ref={(c) => {
                      this.checkBtn = c;
                    }}
                  />
                </Form>
              </Col>
            </Row>
          </Container>
        </section>
      </>
    );
  }
}
