import React from "react"
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
 
import './App.css';
import home from './pages/home';
import Footer from './components/footer';

import Contact from './pages/Contact'
import Header from "./components/header";
import Login from "./components/Authentification/login";
import TopBar from "./components/topbar";
import Partenaire from "./pages/Partenaires/partenaire";
import Registre from "./components/Authentification/registre";
import About from "./components/Home/about";
import Services from "./components/Home/services";
import Profile from "./components/Authentification/profile";
import ForgotPassword from "./components/Authentification/changepassword";
import ResetPassword from "./components/Authentification/resetpassword";
import UpdateProfile from "./components/Authentification/updateprofile";
import OAuth2RedirectHandler from "./components/Authentification/oauth2/OAuth2RedirectHandler";
import listoffres from "./pages/Offres/listoffres";
import AddOffre from "./pages/Offres/AddOffre";
import espaceclient from "./pages/Entreprise/espaceclient";
import resume from "./components/Authentification/resume";
import OffresEntreprise from "./pages/Offres/OffresEntreprise";
import ProfileEntreprise from "./pages/Entreprise/ProfileEntreprise";
import listReponse from "./pages/Offres/listReponse";
import EditOffreEntreprise from "./pages/Entreprise/EditOffreEntreprise";
import OffresFront from "./pages/Offres/OffresFront";


//const Contact = React.lazy(() => import("./components/contact"))


 
function App() {


  return (
    <div className="wrapper">
      <Router>
        <TopBar />
        <Header />
        <Switch>
          <Route path="/strides/contact" component={Contact} />
          <Route path="/strides/client" component={Partenaire} />
          <Route path="/signIn" component={Login} />
          <Route path="/signUp" component={Registre} />
          <Route exact path="/profile"   component={Profile} />
          <Route path="/strides/about" component={About} />
          <Route path="/strides/services" component={Services} />
          <Route path="/login/reset" component={ForgotPassword} />
          <Route path="/reset_password" component={ResetPassword} />
          <Route path="/user/:id" component={UpdateProfile}></Route>
          <Route path="/oauth2/redirect" component={OAuth2RedirectHandler}></Route>
          <Route path="/strides/offres" component={listoffres} />
          <Route path="/strides/add-offres" component={AddOffre} />
          <Route path="/strides/Espace-client" component={espaceclient} />
          <Route path="/offre/:id" component={resume} />
          <Route path="/strides/EditOffres" component={OffresEntreprise} />
          <Route path="/strides/Profile-Client" component={ProfileEntreprise} />
          <Route path="/strides/Suivie-Reponse" component={listReponse} />
          <Route path="/strides/entreprise/edit-offre/:id" component={EditOffreEntreprise} />
          <Route path="/strides/blog" component={OffresFront} />



          <Route exact path="/" component={home} />
          <Redirect to="/" />
        </Switch>
        <Footer />
      </Router>
    </div>
  );
}
export default App;
 