import React, { Component } from 'react';
import 'whatwg-fetch';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import AuthService from "../../services/Authentification/authservices";

const required = value => {
	if (!value) {
	  return (
		<div className="alert alert-danger" role="alert">
		  This field is required!
		</div>
	  );
	}

  };


class ChangePassword extends Component {

	constructor(props) {
		super(props);
		this.handleforgetpass = this.handleforgetpass.bind(this);
		this.onChangeEmail = this.onChangeEmail.bind(this);
	
		this.state = {
		  email: "",
		  loading: false,
		  message: ""
		};
	  }
	
	  onChangeEmail(e) {
		this.setState({
		  email: e.target.value
		});
	  }
	
	  
	
	  handleforgetpass(e) {
        e.preventDefault();
    
        this.setState({
          message: "",
          successful: false
        });
    
        this.form.validateAll();
    
        if (this.checkBtn.context._errors.length === 0) {
          AuthService.forgotpassword(
            this.state.email,
            
          ).then(
            response => {
              this.setState({
                message: response.data.message,
                successful: true
              });
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              this.setState({
                successful: false,
                message: resMessage
              });
            }
          );
        }
      }

    render() {
        return (
            <div class="container">

		<ol class="breadcrumb">
			<li><a href="index.html">Home</a></li>
			<li class="active">changer mot de passe</li>
		</ol>

		<div class="row">

		{/* <header class="page-header">
					<h4 class="page-title">Sign in</h4>
				</header>	 */}

<div class="col-md-6 col-md-offset-3 col-sm-8 col-sm-offset-2">
					<div class="panel panel-default">
						<div class="panel-body">
							<h3 class="thin text-center">Rinitialisez votre mot de passe!</h3>
							<hr />
							
							<Form  onSubmit={this.handleforgetpass}
                                 ref={c => {
                                      this.form = c;
                                    }}>
								<div class="top-margin">
									<label>Email <span class="text-danger">*</span></label>
									<Input 
									type="text" 
									className="form-control"
									value={this.state.email}
                                     onChange={this.onChangeEmail}
                                     validations={[required]} 
									/>
								</div>
								

								<hr />

								<div class="row">
									
									<div class="col-lg-4 text-right">
										<button className="btn btn-action" type="submit" disabled={this.state.loading}>{this.state.loading && (
                  <span className="spinner-border spinner-border-sm"></span>
                )}Send</button>
									</div>
								</div>
								{this.state.message && (
              <div className="form-group">
                <div className="alert alert-danger" role="alert">
                  {this.state.message}
                </div>
              </div>
            )}
             

             {this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}
             <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />

							</Form>
						</div>
					</div>

				</div>
</div>
</div>

        
      )
    }

}
    
    

export default ChangePassword;