import React, { Component } from 'react'
import { Link } from "react-router-dom";
import CustomInput from "reactstrap/lib/CustomInput"
import Label from "reactstrap/lib/Label"
import AuthService from "../../services/Authentification/authservices";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select"
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import UploadService from "../../services/Authentification/UploadFilesUserService";





const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const vusername = value => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The username must be between 3 and 20 characters.
      </div>
    );
  }
};
const vname = value => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The firstName must be between 3 and 20 characters.
      </div>
    );
  }
};

const vprenom = value => {
  if (value.length < 3 || value.length > 20) {
    return (
      <div className="alert alert-danger" role="alert">
        The lastName must be between 3 and 20 characters.
      </div>
    );
  }
};

const vpassword = value => {
  if (value.length < 6 || value.length > 40) {
    return (
      <div className="alert alert-danger" role="alert">
        The password must be between 6 and 40 characters.
      </div>
    );
  }
};

export default class espaceclient extends Component {
  constructor(props) {
    super(props);
    this.handleClient = this.handleClient.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeEntreprise = this.onChangeEntreprise.bind(this);
    this.onChangeEmail = this.onChangeEmail.bind(this);
    this.onChangeTel = this.onChangeTel.bind(this);
    this.onChangeAddress = this.onChangeAddress.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.onChangeSecteur = this.onChangeSecteur.bind(this);
    this.onChangeRole = this.onChangeRole.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangeConfirmPassword = this.onChangeConfirmPassword.bind(this);


    this.state = {
      username: "",
      email: "",
      password: "",
      passwordConfirm: "",
      firstName:"",
      lastName:"",
      entreprise:"",
      address:"",
      tel: "",
      description:"",
      secteur:"",
      roles:"",
      selectedFiles: undefined,
      currentFile: undefined,
      progress: 0,
      successful: false,
      message: "",
      images: [],
    };
  }

  selectFile(event) {
    this.setState({
      selectedFiles: event.target.files,
    });
  }

  onChangeUsername(e) {
    this.setState({
      username: e.target.value
    });
  }

  onChangeFirstName(e) {
    this.setState({
        firstName: e.target.value
    });
  }

  onChangeLastName(e) {
    this.setState({
        lastName: e.target.value
    });
  }

  onChangeEntreprise(e) {
    this.setState({
        entreprise: e.target.value
    });
  }

  onChangeEmail(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }

  onChangeConfirmPassword(e) {
    this.setState({
        passwordConfirm: e.target.value
    });
  }

  onChangeTel(e) {
    this.setState({
      tel: e.target.value
    });
  }

  onChangeAddress(e) {
    this.setState({
      address: e.target.value
    });
  }



  onChangeDescription(e) {
    this.setState({
      description: e.target.value
    });
  }

  onChangeSecteur(e) {
    this.setState({
      secteur: e.target.value
    });
  }

  onChangeRole(e) {
    this.setState({
      roles: e.target.value
    });
  }

  componentDidMount() {
    UploadService.getFiles().then((response) => {
      this.setState({
        fileInfos: response.data,
      });
    });
  }

  changeImagesHandler = (event) => {
    this.setState({ images: event.target.files[0] });
  };

  handleClient(e) {
    e.preventDefault();
    let currentFile = this.state.images;

    this.setState({
      message: "",
      successful: false,
      progress: 0,
      currentFile: currentFile,
    });
    

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.client(
        this.state.username,
        this.state.email,
        this.state.password,
        this.state.passwordConfirm,
        this.state.firstName,
        this.state.lastName,
        this.state.entreprise,
        this.state.address,
        this.state.tel,
        this.state.description,
        this.state.secteur,
        this.state.roles,
        this.state.images,
        
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage,
            selectedFiles: undefined,
          });
        }
      );
      }
  }
    render() {
   
        return (
<section className="testimonial py-5" id="testimonial">
  <div className="container">
    <div className="row ">
      <div className="col-md-4 py-5 bg-success text-white text-center ">
        <div className=" ">
          <div className="card-body">
            <img src="http://www.ansonika.com/mavia/img/registration_bg.svg" style={{width: '30%'}} />
            <h2 className="py-3">Espace Client</h2>
            <p>Crer un profil STRIDES vous aidera  publier des offres d'emploi et de Gagner du temps en nous trouvant les bons candidats
            </p>
          </div>
        </div>
      </div>
      <div className="col-md-8 py-5 border">
        <h4 className="pb-4">Crer votre espace </h4>
        <Form onSubmit={this.handleClient} ref={c => {
                        this.form = c;
                         }} >
                            
                            
                            {!this.state.successful && (          <div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <Input id="Full Name" name="Full Name" placeholder="Username" className="form-control" type="text"
              value={this.state.username}
              onChange={this.onChangeUsername}
              validations={[required, vusername]}
               />
            </div>
            <div className="form-group col-md-6">
              <Input type="email" className="form-control" id="inputEmail4" placeholder="Email" 
               value={this.state.email}
               onChange={this.onChangeEmail}
               validations={[required, email]}

               />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <Input id="Name" name="Name" placeholder="Nom" className="form-control" type="text"
              value={this.state.firstName}
              onChange={this.onChangeFirstName}
              validations={[required, vname]}
              />
            </div>
            <div className="form-group col-md-6">
              <Input id="Prenom" name="Prenom" placeholder="Prenom" className="form-control" type="text"
              value={this.state.lastName}
              onChange={this.onChangeLastName}
              validations={[required, vprenom]}
              />
            </div>
          </div>
          <div className="form-row">
            <div className="form-group col-md-6">
              <Input id="Mobile No." name="Mobile No." placeholder="Tlphone" className="form-control" required="required" type="text" 
               value={this.state.tel}
               onChange={this.onChangeTel}
               validations={[required]}

               />
            </div>
            <div className="form-group col-md-6">
              <Select id="inputState" className="form-control"
                value={this.state.secteur}
                onChange={this.onChangeSecteur}
                >
                <option value="">Secteur d'activit ...</option>
                <option value="Informatique"> Informatique</option>
                <option value="touslessecteurs"> tout les secteurs</option>
                <option value="Comerce"> Commerce</option>
                <option value="Industrie"> Industrie</option>
              </Select>
            </div>
            <div className="form-group col-md-6">
              <Input id="Nom de l'entreprise" name="Full Name" placeholder="Nom de l'entreprise" className="form-control" type="text" 
              value={this.state.entreprise}
              onChange={this.onChangeEntreprise}

              />
            </div>
            <div className="form-group col-md-6">
              <Input type="adresse" className="form-control" id="adresse" placeholder="Emplacement" 
              value={this.state.address}
              onChange={this.onChangeAddress}
              validations={[required]}

              />
            </div>
            <div className="form-group col-md-12">
              <textarea id="comment" name="comment" cols={40} rows={5} className="form-control" defaultValue={""} placeholder="description de l'entreprise"
              value={this.state.description}
              onChange={this.onChangeDescription}
              validations={[required]}

               />
            </div>
            <div className="form-group col-md-6">
              <Input type="password" className="form-control" id="password" placeholder="Password" 
              value={this.state.password}
              onChange={this.onChangePassword}
              validations={[required, vpassword]}

              />
            </div>
            <div className="form-group col-md-6">
              <Input type="password" className="form-control" id="password" placeholder="Confirmer le mot de passe" 
               value={this.state.passwordConfirm}
               onChange={this.onChangeConfirmPassword}
               validations={[required, vpassword]}

               />
            </div>
            
            <div className="form-group col-md-13">
            <Label htmlFor="text-input">Logo de l'entreprise</Label>
            <CustomInput
            id="cover"
            name="cover"
            type="file"
            accept="image/png, image/jpeg, image/jpg"
            onChange={this.changeImagesHandler}
           // onChange={handleInputChange}
          />
                      </div>
                    
          </div>
          <div className="form-row">
            <div className="form-group">
              <div className="form-group">
                <div className="form-check">
                  <Input className="form-check-input" type="checkbox" defaultValue id="invalidCheck2" required />
                  <label className="form-check-label" htmlFor="invalidCheck2">
                    <small>By clicking Submit, you agree to our Terms &amp; Conditions, Visitor Agreement and Privacy Policy.</small>
                  </label>
                </div>
              </div>
            </div>
          </div>
          <div className="form-row">
          <div class="col-lg-8">
                      <b>
                        <Link to="/signIn">Vous avez dj un compte? se connecter</Link>
                      </b>
                    </div>
                    <div class="col-lg-4 text-right">
                      <button
                        className="btn btn-action"
                        type="submit"
                        onClick={this.handleClient}
                      >
                       
                        Enregister
                      </button>
                    </div>
          </div>
          </div>
          )}

 {this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}  

        <CheckButton style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />         

           
        </Form>
      </div>
    </div>
  </div>
</section>



        )
    }
}
