import React, { Component } from "react";
import "whatwg-fetch";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import AuthService from "../../services/Authentification/authservices";
import googleLogo from "./img/google-logo.png";
import { Link } from "react-router-dom";
import { GOOGLE_AUTH_URL } from "../../config/constants";
import { Redirect } from "react-router-dom";
import Alert from "react-s-alert";


const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);

    this.state = {
      username: "",
      password: "",
      loading: false,
      message: "",
    };
  }


  componentDidMount() {
    // If the OAuth2 login encounters an error, the user is redirected to the /login page with an error.
    // Here we display the error and then remove the error query parameter from the location.
    if (this.props.location.state && this.props.location.state.error) {
      setTimeout(() => {
        Alert.error(this.props.location.state.error, {
          timeout: 5000
        });
        this.props.history.replace({
          pathname: this.props.location.pathname,
          state: {}
        });
      }, 100);

    }
  }


  onChangeUsername(e) {
    this.setState({
      username: e.target.value,
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value,
    });
  }

  handleLogin(e) {
    e.preventDefault();

    this.setState({
      message: "",
      loading: true,
    });

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.login(this.state.username, this.state.password).then(
        (res) => {
          if(res.roles[0]==="ROLE_USER"){
          this.props.history.push("/profile");
          localStorage.setItem("ROLE_USER", true)

          }
          else{
            this.props.history.push("/strides/Profile-client");
            localStorage.setItem("ROLE_ENTREPRISE", true)
          }

          window.location.reload();
          console.log(res);
          
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          this.setState({
            loading: false,
            message: resMessage,
          });
        }
      );
    } else {
      this.setState({
        loading: false,
      });
    }
  }

  render() {
    
	if (this.props.authenticated) {
		return (
		  <Redirect
			to={{
			  pathname: "/",
			  state: { from: this.props.location }
			}}
		  />
		);
	  }
    return (
      <div class="container">
        <ol class="breadcrumb">
          <li>
            <a href="index.html">Home</a>
          </li>
          <li class="active">Accs utilisateur</li>
        </ol>

        <div class="row">
          {/* <header class="page-header">
					<h4 class="page-title">Sign in</h4>
				</header>	 */}

          <div class="col-md-6 col-md-offset-3 col-sm-8 col-sm-offset-2">
            <div class="panel panel-default">
              <div class="panel-body">
                <h3 class="thin text-center">Connectez-vous  votre compte</h3>
                <p class="text-center text-muted">
                  voluer dans votre carrire,faire <a href="/signUp">Register</a>{" "}
                  si vous n'avez pas un compte. la devise de notre cabinet de recrutement
                  c'est le respect les candidats et les clients avant tout!{" "}
                </p>
                <hr />

                <Form
                  onSubmit={this.handleLogin}
                  ref={(c) => {
                    this.form = c;
                  }}
                >
                  <div class="top-margin">
                    <label>
                      username <span class="text-danger">*</span>
                    </label>
                    <Input
                      type="text"
                      className="form-control"
                      value={this.state.username}
                      onChange={this.onChangeUsername}
                      validations={[required]}
                    />
                  </div>
                  <div class="top-margin">
                    <label>
                      Mot de passe <span class="text-danger">*</span>
                    </label>
                    <Input
                      type="password"
                      className="form-control"
                      value={this.state.password}
                      onChange={this.onChangePassword}
                      validations={[required]}
                    />
                  </div>

                  <hr />

                  <div class="row">
                    <div class="col-lg-8">
                      <b>
                        <Link to="/login/reset">mot de passe oubli?</Link>
                      </b>
                    </div>
                    <div class="col-lg-4 text-right">
                      <button
                        className="btn btn-action"
                        type="submit"
                        disabled={this.state.loading}
                      >
                        {this.state.loading && (
                          <span className="spinner-border spinner-border-sm"></span>
                        )}
                        Sign in
                      </button>
                    </div>
                    {/* 	<div className="social-login">
        <a className="btn btn-block social-btn google" href={GOOGLE_AUTH_URL} currentUser={this.state.currentUser}>
          <img src={googleLogo} alt="Google" /> Log in with Google
        </a>
      </div> */}
                    <div className="row">
                      <div className="col-md-12">
                        {" "}
                        <a
                          className="btn btn-lg btn-google btn-block btn-outline"
                          href={GOOGLE_AUTH_URL}
                          currentUser={this.state.currentUser}
                        >
                          <img src="https://img.icons8.com/color/16/000000/google-logo.png" />{" "}
                          Sign in with Google
                        </a>{" "}
                      </div>
                    </div>
                  </div>
                  {this.state.message && (
                    <div className="form-group">
                      <div className="alert alert-danger" role="alert">
                        {this.state.message}
                      </div>
                    </div>
                  )}
                  <CheckButton
                    style={{ display: "none" }}
                    ref={(c) => {
                      this.checkBtn = c;
                    }}
                  />
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
