import React, { Component } from 'react'
import action from "../../services/offreservices/actionoffre";
import Moment from "react-moment";
import AuthService from "../../services/Authentification/authservices";



export default class listReponse extends Component {
    constructor(props) {
        super(props)

        this.state = {
            name: this.props.name,
            email:"",
            localisation: "",
            description: "",
            dateAjout: "",
            timeStart: "",
            timeEnd: "",
          
                candidatures: [],
                qrcodes: [],
        }


        
    }

    componentDidMount(){
        const user = AuthService.getCurrentUser();

        action.getOffreCandidat(user.id).then((res) => {
            this.setState({ candidatures: res.data});
        });
        action.getCandidatureWithQrcode(this.state.id).then((res) => {
          this.setState({ qrcodes: res.data });
        });
    }
    render() {
        const { qrcodes } = this.state;
        const CANDIDATURE_API_BASE_URL = "http://localhost:8084/Candidature";

        return (
            
            <div class="container mt-5 mb-5">
                
            <div class="row d-flex justify-content-center">
            <div class="section-title">
            <h2>Suivi de mes rponses</h2>
            <p>
              Suivez toujours vos rponses.
            </p>
          </div>
          {
                                              this.state.candidatures.map(
                                                  candidature =>
          <div className="row p-2 bg-white border rounded mt-2">
  {/* <div className="col-md-3 mt-1"><img className="img-fluid img-responsive rounded product-image" src={`${CANDIDATURE_API_BASE_URL}/qrcode/${candidature.id}`} /></div> */}
  <div className="col-md-6 mt-1">
    <h3 className="text-primary">{candidature.title}</h3>
    <div className="d-flex flex-row">
      {/* <div className="ratings mr-2"><i className="fa fa-star" /><i className="fa fa-star" /><i className="fa fa-star" /><i className="fa fa-star" /></div><span>110</span> */}
    </div>
    {/* <div className="mt-1 mb-1 spec-1"><span>100% cotton</span><span className="dot" /><span>Light weight</span><span className="dot" /><span>Best finish<br /></span></div>
    <div className="mt-1 mb-1 spec-1"><span>Unique design</span><span className="dot" /><span>For men</span><span className="dot" /><span>Casual<br /></span></div> */}
    <p className="text-justify text-truncate text-success para mb-0">{candidature.description}<br /><br /></p>
  </div>
  <div className="align-items-center align-content-center col-md-3 border-left mt-1">
    <div className="d-flex flex-row align-items-center">
      <h4 className="mr-1 text-danger">{candidature.dateExpiration}</h4><span className="strike-text text-secondary"> Offre Expir</span>
    </div>
    <h5 className="text-success">{candidature.nbreposte} poste ouvert</h5>
<div>
<h5 className="text-info">Etat candidature : <span className="strike-text text-warning"> {candidature.status}</span></h5>

</div>
    {/* <div className="d-flex flex-column mt-4"><button className="btn btn-primary btn-sm" type="button">Details</button><button className="btn btn-outline-primary btn-sm mt-2" type="button">Add to wishlist</button></div> */}
  </div>

</div>
                                              )
    }

            </div>
        </div>

        )
    }
}
