import axios from 'axios';


const CONTACTER_API_BASE_URL = "http://localhost:8084/contact/contacter";





class ContactServices {


    Envoyer(Contact){
        return axios.post(CONTACTER_API_BASE_URL, Contact);
    }

    
    getContact(){
        return axios.get(CONTACTER_API_BASE_URL + '/' + "nom");
    }

  

}

export default new ContactServices ()