import axios from 'axios';


const COMPETENCE_API_BASE_URL = "http://localhost:8084/Info_Prof";
const EXPERIENCE_API_BASE_URL = "http://localhost:8084/Experience";
const EDUCATION_API_BASE_URL = "http://localhost:8084/Education";
const CV_API_BASE_URL = "http://localhost:8084/cv";







class CvServices {


    Validercomp(iduser, InformationsProfessionnelles){
        

        return axios.post(COMPETENCE_API_BASE_URL + "/addCompetence/" + iduser, InformationsProfessionnelles);
    }

    ValiderExperience(iduser, Experience){
        return axios.post(EXPERIENCE_API_BASE_URL + '/addExperience/' + iduser, Experience);
    }

    
    ValiderEducation(idcand, education){
        
        return axios.post(EDUCATION_API_BASE_URL + '/addEducation/' + idcand, education);
    }

    ValiderCv(iduser, file){
        let formData = new FormData();
        formData.append("file", file);
        return axios.post(CV_API_BASE_URL + '/addCv/' + iduser, formData);
    }

    getCVByUser(iduser){
        return axios.get(CV_API_BASE_URL + '/getCv/' + iduser);
    }


    getEducationByUser(iduser){
        return axios.get(EDUCATION_API_BASE_URL + '/find_Education/' + iduser);
    }

    getExperienceByUser(iduser){
        return axios.get(EXPERIENCE_API_BASE_URL + '/find_Experience/' + iduser);
    }

    getCompetenceByUser(iduser){
        return axios.get(COMPETENCE_API_BASE_URL + '/find_Comptence/' + iduser);
    }



    
  

  

}

export default new CvServices ()