import axios from 'axios';

const OFFRE_API_BASE_URL = "http://localhost:8084/offres";
const CANDIDATURE_API_BASE_URL = "http://localhost:8084/Candidature";


class ActionOffre {

    getEmployees(){
        return axios.get(OFFRE_API_BASE_URL + "/findoffre");
    }

    getAll(params) {
        return axios.get(OFFRE_API_BASE_URL + "/find", { params });
      }

      findByTitle(title) {
        return axios.get(OFFRE_API_BASE_URL + '/recherche/' + title);
      }

      findByAdresse(adresse) {
        return axios.get(OFFRE_API_BASE_URL + '/recherche-adresse/' + adresse);
      }

    createProduct(IdUser, offre, file){
        console.log(offre);
        let formData = new FormData();
        formData.append("offre", JSON.stringify(offre));
        formData.append("file", file);

        return axios.post(OFFRE_API_BASE_URL + "/add-offre/" + IdUser, formData);
    }

    updateOffre(offre, id, IdUser, file) {
        console.log(offre);
        let formData = new FormData();
        formData.append("offre", JSON.stringify(offre));
        formData.append("file", file);
        return axios.put(OFFRE_API_BASE_URL + `/edit/${id}/${IdUser}`, formData);
      }

    getOffreById(id){
        return axios.get(OFFRE_API_BASE_URL + '/offre/' + id);
    }

    getOffreByUser(iduser){
        return axios.get(OFFRE_API_BASE_URL + '/Offre-Entreprise/' + iduser);
    }

    updateEmployee(employee, employeeId){
        return axios.put(OFFRE_API_BASE_URL + '/' + employeeId, employee);
    }

    deleteOffre(id){
        return axios.delete(OFFRE_API_BASE_URL + '/delete/' + id);
    }

    ValiderCandidature(Candidature){
        return axios.post(CANDIDATURE_API_BASE_URL + '/valider' ,Candidature);
    }

    EnvoyerCandidature(IdUser, IdOffre, candidature, file){
        console.log(candidature);
        let formData = new FormData();
        formData.append("candidature", JSON.stringify(candidature));
        formData.append("file", file);

        return axios.post(CANDIDATURE_API_BASE_URL + "/add-candidature/" + IdUser + "/" + IdOffre, formData);
    }

    getOffreCandidat(idCandidature){
        return axios.get(CANDIDATURE_API_BASE_URL + '/getOffrePostule/' + idCandidature);
    }
    getCandidatureWithQrcode(id){
        return axios.get(CANDIDATURE_API_BASE_URL + '/qrcode/' + id);
    }

    getNombrePostulation(iduser){
        return axios.get(CANDIDATURE_API_BASE_URL + '/nombrePostulation/' + iduser);
    }

    getNbreOffre(){
        return axios.get(OFFRE_API_BASE_URL + '/NombreOffre');
    }

    ValiderEtat(candidature, id) {
        console.log(candidature);
        let formData = new FormData();
        formData.append("candidature", JSON.stringify(candidature));
        return axios.put(CANDIDATURE_API_BASE_URL + `/valider/${id}`, formData);
      }

}

export default new ActionOffre()