import React, { Component } from 'react'
import { Container } from 'react-bootstrap';
import AuthService from "../../services/Authentification/authservices";
import CvServices from "../../services/CvServices/cvservices";
import action from "../../services/offreservices/actionoffre";
import DownloadButton from "../../utils/DownloadButton"
import arrowsrc from "../../assests/img/arrow.ico"
import { Col, Row } from "react-bootstrap"




const deximeAnne = {
  //link: file2,
  text: "Tlcharger   ",
  src: arrowsrc,
}

class UpdateProfile extends Component {
    constructor(props) {
        super(props)
        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeFirstName = this.onChangeFirstName.bind(this);
        this.onChangeLastName = this.onChangeLastName.bind(this);
        this.onChangeEmail = this.onChangeEmail.bind(this);
        this.onChangeTel = this.onChangeTel.bind(this);
        this.onChangeAddress = this.onChangeAddress.bind(this);
        this.onChangeDateN = this.onChangeDateN.bind(this);
        this.getTutorial = this.getTutorial.bind(this);
       // this.updateCandidature = this.updateCandidature.bind(this);
       this.updateTutorial = this.updateTutorial.bind(this);


        this.state = {
          cv:"",
          cvcandidats: [],
          currentTutorial: {
            user:[],
            id: null,
            username:"",
            email: "", 
            nom:"",
            prenom:"",
            address:"",
            dateN:"",
            tel: "",
            password:"",
            passwordConfirm:"",
            currentFile: undefined,
            images: [],
            
            a:"",
            o:"",
            
          },
          message: ""
        };
           
        
        
    }

    changeImagesHandler = (event) => {
      this.setState({ images: event.target.files[0] });
    };

    onChangeUsername(e) {
      const username = e.target.value;


    this.setState(function(prevState) {
      return {
        currentTutorial: {
          ...prevState.currentTutorial,
          username: username
        }
      };
    });
      }

      onChangeFirstName(e) {
       
       const nom= e.target.value

       this.setState(prevState => ({
        currentTutorial: {
          ...prevState.currentTutorial,
          nom: nom
        }
      }));
      }

      onChangeLastName(e) {
        const prenom= e.target.value

        this.setState(prevState => ({
         currentTutorial: {
           ...prevState.currentTutorial,
           prenom: prenom
         }
       }));
      }
    
      onChangeEmail(e) {
        const email= e.target.value

        this.setState(prevState => ({
         currentTutorial: {
           ...prevState.currentTutorial,
           email: email
         }
       }));
      }
    
    

      onChangeTel(e) {
        const tel= e.target.value

        this.setState(prevState => ({
         currentTutorial: {
           ...prevState.currentTutorial,
           tel: tel
         }
       }));
      }

      onChangeAddress(e) {
        const address= e.target.value

        this.setState(prevState => ({
         currentTutorial: {
           ...prevState.currentTutorial,
           address: address
         }
       }));
      }

      onChangeDateN(e) {
        const dateN= e.target.value

        this.setState(prevState => ({
         currentTutorial: {
           ...prevState.currentTutorial,
           dateN: dateN
         }
       }));
      }

     

      /* onChangePassword(e) {
       const password= e.target.value

       this.setState(prevState => ({
        currentTutorial: {
          ...prevState.currentTutorial,
          password: password
        }
      }));
      } */

     /*  onChangeConfirmPassword(e) {
       const passwordConfirm= e.target.value

       this.setState(prevState => ({
        currentTutorial: {
          ...prevState.currentTutorial,
          passwordConfirm: passwordConfirm
        }
      }));
      } */

   

    componentDidMount() {
      this.getTutorial(this.props.match.params.id);
      CvServices.getCVByUser(this.props.match.params.id).then((res) => {
        this.setState({ cvcandidats: res.data });
      });
      action.getNombrePostulation(this.props.match.params.id).then((res) => {
        this.setState({ a: res.data });
      });
      action.getNbreOffre().then((res) => {
        this.setState({ o: res.data });
      });
    }


    getTutorial(id) {
      AuthService.getEmployeeById(id)
        .then(response => {
          this.setState({
            currentTutorial: response.data
          });
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    }

   /*  updateCandidature = (e) => {
      e.preventDefault();
      let user = {nom: this.state.nom, prenom: this.state.prenom, address:this.state.address, dateN:this.state.dateN, tel: this.state.tel, email: this.state.email};
      console.log('user => ' + JSON.stringify(user));
      console.log('id => ' + JSON.stringify(this.state.id));
      AuthService.updateEmployee(user, this.state.id).then( res => {
          this.props.history.push('/update-profile');
      });
  } */
   
  updateTutorial() {

    AuthService.update(
      this.state.currentTutorial.id,
      this.state.currentTutorial)
      .then(response => {
        console.log(response.data);
        console.log(response.images);

        this.setState({
          message: "The tutorial was updated successfully!"
        });
      })
      .catch(e => {
        console.log(e);
      });
  }
    cancel(){
        this.props.history.push('/profile');
    }

    render() {
      const { currentTutorial } = this.state;
      const { cvcandidats } = this.state;
      const { a , o } = this.state;

        return (
          <Container>
            <ol className="breadcrumb">
			<li><a href="index.html">Home</a></li>
			<li className="active">Modifier Profile</li>
		</ol>
          <div class="container bootstrap snippet">
            
          
          <div class="row">
            <div class="col-sm-3">
                    
      
            <div class="text-center">
              <img src={currentTutorial.images && currentTutorial.images[0]?.image} class="avatar img-circle img-thumbnail" alt="avatar" />
              <h6>Upload a different photo...</h6>
              <input type="file" class="text-center center-block file-upload" 
              onChange={this.changeImagesHandler}/>
              
            </div>
         
                <div class="panel panel-default">
                  <div class="panel-heading">User name: <i class="fa fa-link fa-1x"></i></div>
                  <input placeholder="username" name="firstName" className="form-control" 
                                                value={currentTutorial.username} onChange={this.onChangeUsername}/>
                </div>
                
                
                <ul class="list-group">
                  <li class="list-group-item text-muted">Activity <i class="fa fa-dashboard fa-1x"></i></li>
                  <li class="list-group-item text-right"><span class="pull-left"><strong>Offres Sur Site</strong></span> {o}</li>
                  <li class="list-group-item text-right"><span class="pull-left"><strong>Postulations</strong></span> {a}</li>
                  {/* <li class="list-group-item text-right"><span class="pull-left"><strong>Vues</strong></span> 78</li> */}
                </ul> 
                     
                <div class="panel panel-default">
                  <div class="panel-heading">Mon CV</div>
                  {cvcandidats &&
                          cvcandidats.map((cv_candidat) => (
                  <div class="panel-body">
                           <a href={cv_candidat.cv} download="My_File.pdf" target="_blank"> Tlcharger </a>

                           <style
                      dangerouslySetInnerHTML={{
                        __html: [
                          "   .download-button {",
                          "  height: 10px!important;",
                          "   font-size:14px!important;",
                          "   float:left!important;",
                          "}",
                        ].join("\n"),
                      }}></style>
                    {/* <DownloadButton data={deximeAnne} /> */}

                 </div>
                            ))} 
                </div>
                
              </div>
            
            <div class="col-sm-9">
                  <ul class="nav nav-tabs">
                      <li class="active"><a data-toggle="tab" href="#home">Profile</a></li>
                      
                    </ul>
      
                    
                <div class="tab-content">
                  <div class="tab-pane active" id="home">
                        <form >
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                    <label for="first_name"><h4>First name</h4></label>
                                    <input type="text" class="form-control" name="first_name" id="first_name" placeholder="first name" title="enter your first name if any."
                                     value={currentTutorial.nom} onChange={this.onChangeFirstName}/>
                                </div>
                            </div>
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                  <label for="last_name"><h4>Last name</h4></label>
                                    <input type="text" class="form-control" name="last_name" id="last_name" placeholder="last name" title="enter your last name if any."
                                    value={currentTutorial.prenom} onChange={this.onChangeLastName}/>
                                </div>
                            </div>
                
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                    <label for="phone"><h4>Date Naissance</h4></label>
                                    <input  
                         name="requested_order_ship_date" 
                         type="date" 
                        value={ currentTutorial.dateN  } 
                         className="form-control" 
                         onChange={ this.onChangeDateN }
                         />
                                </div>
                            </div>
                
                            <div class="form-group">
                                <div class="col-xs-6">
                                   <label for="mobile"><h4>Mobile</h4></label>
                                    <input type="text" class="form-control" name="mobile" id="mobile" placeholder="enter mobile number" title="enter your mobile number if any."
                                    value={currentTutorial.tel}
                                    onChange={this.onChangeTel}/>
                                </div>
                            </div>
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                    <label for="email"><h4>Email</h4></label>
                                    <input type="email" class="form-control" name="email" id="email" placeholder="you@email.com" title="enter your email."
                                    value={currentTutorial.email} onChange={this.onChangeEmail}/>
                                </div>
                            </div>
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                    <label for="adress"><h4>Location</h4></label>
                                    <input type="adress" class="form-control" id="location" placeholder="somewhere" title="enter a location"
                                     value={currentTutorial.address}
                                     onChange={this.onChangeAddress}/>
                                </div>
                            </div>
                           {/*  <div class="form-group">
                                
                                <div class="col-xs-6">
                                    <label for="password"><h4>Password</h4></label>
                                    <input type="password" class="form-control" name="password" id="password" placeholder="password" title="enter your password."
                                    value={currentTutorial.password}
                                   />
                                </div>
                            </div>
                            <div class="form-group">
                                
                                <div class="col-xs-6">
                                  <label for="password2"><h4>Verify</h4></label>
                                    <input type="password" class="form-control" name="password2" id="password2" placeholder="password2" title="enter your password2."
                                     value={currentTutorial.passwordConfirm}
                                     />
                                </div>
                            </div> */}
                            <div class="form-group">
                                 <div class="col-xs-12">
                                 <button className="btn btn-success" onClick={this.updateTutorial}>Save</button>
                                <button className="btn btn-danger" onClick={this.cancel.bind(this)} style={{marginLeft: "10px"}}>Cancel</button>
                                  </div>
                            </div>
                      </form>
                    
                    
                   </div>
                                      
                    </div>
                </div>
      
              </div>
          </div>
          </Container>     
        )
    }
}

export default UpdateProfile