import React, { Component } from 'react';
import { Container, Row } from 'react-bootstrap';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import Select from "react-validation/build/select"
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";
import CustomInput from "reactstrap/lib/CustomInput"
import AuthService from "../../services/Authentification/authservices";

const required = value => {
    if (!value) {
      return (
        <div className="alert alert-danger" role="alert">
          This field is required!
        </div>
      );
    }
  };
  
  const email = value => {
    if (!isEmail(value)) {
      return (
        <div className="alert alert-danger" role="alert">
          This is not a valid email.
        </div>
      );
    }
  };
  
  const vusername = value => {
    if (value.length < 3 || value.length > 20) {
      return (
        <div className="alert alert-danger" role="alert">
          The username must be between 3 and 20 characters.
        </div>
      );
    }
  };
  const vname = value => {
    if (value.length < 3 || value.length > 20) {
      return (
        <div className="alert alert-danger" role="alert">
          The firstName must be between 3 and 20 characters.
        </div>
      );
    }
  };

  const vprenom = value => {
    if (value.length < 3 || value.length > 20) {
      return (
        <div className="alert alert-danger" role="alert">
          The lastName must be between 3 and 20 characters.
        </div>
      );
    }
  };

  const vpassword = value => {
    if (value.length < 6 || value.length > 40) {
      return (
        <div className="alert alert-danger" role="alert">
          The password must be between 6 and 40 characters.
        </div>
      );
    }
  };


  

class Registre extends Component {

    constructor(props) {
        super(props);
        this.handleRegister = this.handleRegister.bind(this);
        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeFirstName = this.onChangeFirstName.bind(this);
        this.onChangeLastName = this.onChangeLastName.bind(this);
        this.onChangeEmail = this.onChangeEmail.bind(this);
        this.onChangeTel = this.onChangeTel.bind(this);
        this.onChangeAddress = this.onChangeAddress.bind(this);
        this.onChangeDateN = this.onChangeDateN.bind(this);
        this.onChangeSexe = this.onChangeSexe.bind(this);
        this.onChangeRole = this.onChangeRole.bind(this);
        this.onChangePassword = this.onChangePassword.bind(this);
        this.onChangeConfirmPassword = this.onChangeConfirmPassword.bind(this);

    
        this.state = {
          username: "",
          email: "",
          password: "",
          passwordConfirm: "",
          firstName:"",
          lastName:"",
          address:"",
          dateN:"",
          tel: "",
          sexe:"",
          roles:"",
          successful: false,
          message: "",
          images: [],
        };
      }
    
      onChangeUsername(e) {
        this.setState({
          username: e.target.value
        });
      }

      onChangeFirstName(e) {
        this.setState({
            firstName: e.target.value
        });
      }

      onChangeLastName(e) {
        this.setState({
            lastName: e.target.value
        });
      }
    
      onChangeEmail(e) {
        this.setState({
          email: e.target.value
        });
      }
    
      onChangePassword(e) {
        this.setState({
          password: e.target.value
        });
      }

      onChangeConfirmPassword(e) {
        this.setState({
            passwordConfirm: e.target.value
        });
      }

      onChangeTel(e) {
        this.setState({
          tel: e.target.value
        });
      }

      onChangeAddress(e) {
        this.setState({
          address: e.target.value
        });
      }

      onChangeDateN(e) {
        this.setState({
          dateN: e.target.value
        });
      }

      onChangeSexe(e) {
        this.setState({
          sexe: e.target.value
        });
      }

      onChangeRole(e) {
        this.setState({
          roles: e.target.value
        });
      }

      changeImagesHandler = (event) => {
        this.setState({ images: event.target.files[0] });
      };

      handleRegister(e) {
        e.preventDefault();
        let currentFile = this.state.images;
    
        this.setState({
          message: "",
          successful: false,
          progress: 0,
          currentFile: currentFile,
        });
    
        this.form.validateAll();
    
        if (this.checkBtn.context._errors.length === 0) {
          AuthService.register(
            this.state.username,
            this.state.email,
            this.state.password,
            this.state.passwordConfirm,
            this.state.firstName,
            this.state.lastName,
            this.state.address,
            this.state.dateN,
            this.state.tel,
            this.state.sexe,
            this.state.roles,
            this.state.images,
          ).then(
            response => {
              this.setState({
                message: response.data.message,
                successful: true
              });
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              this.setState({
                successful: false,
                message: resMessage
              });
            }
          );
        }
      }
    
    
    render() {
        return (

<Container>

<ol className="breadcrumb">
    <li><a href="index.html">Home</a></li>
    <li className="active">Registration</li>
</ol>

<Row>
    
       {/*  <header className="page-header">
            <h1 className="page-title">Registration</h1>
        </header> */}
        
        <div className="col-md-6 col-md-offset-3 col-sm-8 col-sm-offset-2">
            <div className="panel panel-default">
                <div className="panel-body">
                    <h3 className="thin text-center">Inscription candidat</h3>
                    <p className="text-center text-muted">si vous avez un compte faire, <a href="/signIn">Login</a> pour accder  votre compte. Strides Human Capital vous offre plus de services suivant votre besoins. </p>
                    <hr />

                    <Form  onSubmit={this.handleRegister}
                       ref={c => {
                        this.form = c;
                         }}
                   >
                       {!this.state.successful && (
              <div>
                        <div className="top-margin">

                            <label>Username</label>
                            <Input
                             type="text"
                              className="form-control"
                              value={this.state.username}
                              onChange={this.onChangeUsername}
                              validations={[required, vusername]}
                               />
                        </div>
                        <div className="top-margin">
                            <label>First Name</label>
                            <Input 
                            type="text" 
                            className="form-control" 
                            value={this.state.firstName}
                            onChange={this.onChangeFirstName}
                            validations={[required, vname]}
                            />
                        </div>
                        <div className="top-margin">
                            <label>Last Name</label>
                            <Input type="text" 
                            className="form-control" 
                            value={this.state.lastName}
                            onChange={this.onChangeLastName}
                            validations={[required, vprenom]}
                            />
                        </div>
                        <div className="top-margin">
                            <label>Email Address <span className="text-danger">*</span></label>
                            <Input
                             type="text"
                              className="form-control"
                              value={this.state.email}
                               onChange={this.onChangeEmail}
                              validations={[required, email]}
                              />
                        </div>
                         
                        <div className="top-margin">
                            <label>Sexe </label>
                            <Select
                              className="form-control"
                              name="sexe"
                              id="sexe"
                              value={this.state.sexe}
                              onChange={this.onChangeSexe}
                             // validations={[required]}
                              >
                              <option value=""></option>
                              <option value= "HOMME">HOMME</option>
                              <option value="FEMME">FEMME</option>
                            </Select>
                          </div>


                          <div className="top-margin">
                            <label>Address</label>
                            <Input type="text" 
                            className="form-control" 
                            value={this.state.address}
                            onChange={this.onChangeAddress}
                            validations={[required]}
                            />
                        </div>
                        <div className="top-margin">
                        <label>Date Naissance <span className="text-danger">*</span></label>
                        <input  
                         name="requested_order_ship_date" 
                         type="date" 
                        value={ this.state.dateN  } 
                         className="form-control" 
                         onChange={ this.onChangeDateN }
                         validations={[required]}
                         />
                        </div>

                        <div className="top-margin">
                            <label>Tel <span className="text-danger">*</span></label>
                            <Input
                             name="number"
                             id="number"
                             className="form-control"
                             type="text"
                             autoComplete="off"
                              value={this.state.tel}
                               onChange={this.onChangeTel}
                             validations={[required ]}
                              />
                        </div>
                       
                      <div>
                        <div className="top-margin">
                            <label>Image <span className="text-danger">*</span></label>
                            <CustomInput
            id="cover"
            name="cover"
            type="file"
            accept="image/png, image/jpeg, image/jpg"
            onChange={this.changeImagesHandler}
           // onChange={handleInputChange}
          />
                            {/* <Select
                              name="role"
                              id="role"
                              className="form-input"
                              value={this.state.roles}
                               onChange={this.onChangeRole}
                              validations={[required]}
                              >
                              <option value=""></option>
                              <option value="ROLE_USER">Candidature</option>
                              <option value="ROLE_ADMIN">Entreprise</option>
                            </Select> */}
                          </div>
                       </div>

                        <div className="row top-margin">
                            <div className="col-sm-6">
                                <label>Password <span className="text-danger">*</span></label>
                                <Input
                                 type="password"
                                  className="form-control" 
                                  value={this.state.password}
                                  onChange={this.onChangePassword}
                                  validations={[required, vpassword]}
                                  />
                            </div>
                            <div className="col-sm-6">
                                <label>Confirm Password <span className="text-danger">*</span></label>
                                <Input
                                 type="password"
                                  className="form-control"  
                                  value={this.state.passwordConfirm}
                                  onChange={this.onChangeConfirmPassword}
                                  validations={[required, vpassword]}
                                  />
                            </div>
                        </div>

                        <hr />

                        <div className="row">
                            <div className="col-lg-8">
                                <label className="checkbox">
                                    <input type="checkbox" /> 
                                    I've read the <a href="page_terms.html" >Terms and Conditions</a>
                                </label>                        
                            </div>
                            <div className="col-lg-4 text-right">
                                <button className="btn btn-action" type="submit">Register</button>
                            </div>
                            
                            
                        </div>

                        </div>
                                    )}

{this.state.message && (
              <div className="form-group">
                <div
                  className={
                    this.state.successful
                      ? "alert alert-success"
                      : "alert alert-danger"
                  }
                  role="alert"
                >
                  {this.state.message}
                </div>
              </div>
            )}
            <CheckButton
              style={{ display: "none" }}
              ref={c => {
                this.checkBtn = c;
              }}
            />         

                        
                    </Form>
                    
                </div>
                
            </div>

        </div>
        
    

</Row>
</Container>	

        )
    }
}

export default Registre;