import React, { Component } from 'react'
import AuthService from "../../services/Authentification/authservices";
import { getCurrentUser } from "../../services/Authentification/oauthservices";
import { ACCESS_TOKEN } from "../../config/constants";
import LinkDuo from "../../utils/LinkDuo";
import EventBus from "../../components/Authentification/common/EventBus";
import action from "../../services/offreservices/actionoffre";




export default class ProfileEntreprise extends Component {
  constructor(props) {
    super(props);

    this.deleteOffre = this.deleteOffre.bind(this);

    this.state = {
      loading: false,
      show: false,
      redirect: null,
      userReady: false,
      currentTutorial: null,
      currentIndex: -1,
      currentUser: { username: "" },
      id: null,
      nom: "",
      prenom: "",
      address: "",
      dateN: "",
      tel: "",
      descripssion:"",
      users: [],
      offres:[],
      agreement: false,

     

    };
 
  }


  deleteOffre(id) {
    action.deleteOffre(id).then((res) => {
      this.setState({
        offres: this.state.offres.filter((offre) => offre.id !== id),
      });
    });
  }

  DetailOffre(id) {
    this.props.history.push(`/strides/entreprise/edit-offre/${id}`);
  }

  componentDidMount() {
    const user = AuthService.getCurrentUser();
    getCurrentUser().then((response) => {
      this.setState({
        currentUser: response,
        authenticated: true,
        loading: false,
      });
    });
    if (user) {
      this.setState({
        currentUser: user,
        showAdminBoard: user.roles.includes("ROLE_ENTREPRISE"),
      });
      this.setState({ user: user, userReady: true });
    }

    EventBus.on("logout", () => {
      this.handleLogout();
    });

    action.getOffreByUser(user.id).then((res) => {
      this.setState({ offres: res.data });
    });

  
  }
    render() {
      const { currentUser } = this.state;
      const { offres } = this.state;


        return (
           
<main>
  <div className="container">
    <div className="row">
      <div className="col-md-12">
        <div className="top-breadcrumb">
          <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
              <li className="breadcrumb-item"><a href="index.html">Home</a></li>
              <li className="breadcrumb-item active" aria-current="page">Entreprise</li>
            </ol>
          </nav>
        </div>
      </div>
    </div>
    <img className="img" src={currentUser.images && currentUser.images[0]?.image}  height={500} alignItems={'center'} justifyContent={'center'} backgroundSize={'cover'}/>
    <div className="card social-prof">
      <div className="card-body">
       
        <div className="row ">
          <div className="col-lg-12">
            <ul className=" nav nav-tabs justify-content-center s-nav">
              
              <li>
              <LinkDuo className="active" to="/strides/add-offres">Offre d'emplois </LinkDuo>

                {/* <a className="active" href="/strides/add-offres">Offre d'emplois</a> */}
                </li>
              {/* <li><a href="#">Annonce de formations</a></li> */}
              <li><a href="#">Modifier Offre</a></li>
              <li><a href="#">Photos</a></li>
              <li><a href="#">Videos</a></li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div className="row">
      <div className="col-lg-3">
        <div className="card">
          <div className="card-body">
            <div className="h5 text-blue">{currentUser.username}</div>
            <div className="h7 "><strong>Name :</strong>  {currentUser.firstName} {currentUser.lastName}</div>
            <div className="h7"><strong>About :</strong> {currentUser.entreprise}
            </div>
          </div>
          <ul className="list-group list-group-flush">
            <li className="list-group-item">
              <div className="h6 text-muted">Email :</div>
              <div className="h5">{currentUser.email}</div>
            </li>
            <li className="list-group-item">
              <div className="h6 text-muted">Adress :</div>
              <div className="h5">{currentUser.address}</div>
            </li>
            <li className="list-group-item">
              <div className="h6 text-muted">Tlphone : </div>
              <div className="h5">{currentUser.tel}</div>
            </li>
          </ul>
        </div>
       
      </div>
      <div className="col-lg-6 gedf-main">
        {/*- \\\\\\\Post*/}
        <div className="card social-timeline-card">
          <div className="card-header">
            <ul className="nav nav-tabs card-header-tabs" id="myTab" role="tablist">
              <li className="nav-item">
                <a className="nav-link active" id="posts-tab" data-toggle="tab" href="#posts" role="tab" aria-controls="posts" aria-selected="true">Liste des offres</a>
              </li>
              <li className="nav-item">
              </li>
            </ul>
          </div>
         
        </div>
        {/* Post /////*/}
        {/*- \\\\\\\Post*/}
       
        {/* Post /////*/}
        {/*- \\\\\\\Post*/}
        <div>
        {offres &&
                          offres.map((offre) => (
        <div className="card social-timeline-card">
          <div className="card-header">
            <div className="d-flex justify-content-between align-items-center">
              <div className="d-flex justify-content-between align-items-center">
                <div className="mr-2">
                  <img className="rounded-circle" width={45} src={offre.images && offre.images[0]?.image} alt />
                </div>
                <div className="ml-2">
                  <div className="h5 m-0 text-blue">Titre : {offre.title}</div>
                  <div className="h7 text-muted">Nombre Poste: {offre.nbreposte}</div>
                </div>
              </div>
              <div>
                <div className="dropdown">
                  <button className="btn btn-link dropdown-toggle" type="button" id="gedf-drop1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    <i className="fa fa-ellipsis-h" />
                  </button>
                  <div className="dropdown-menu dropdown-menu-right" aria-labelledby="gedf-drop1" key={offre.id}>
                    <div className="h6 dropdown-header">Configuration</div>
                    <a className="dropdown-item"  onClick={() => this.DetailOffre(offre.id)} >Edit</a>
                    {/* <a className="dropdown-item" >Hide</a> */}
                    <button className="dropdown-item" onClick={() => this.deleteOffre(offre.id)}>delete</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="card-body">
            <div className="text-muted h7 mb-2"> <i className="fa fa-clock-o" /> {offre.dateExpiration}</div>
            <a className="card-link" href="#">
              <h5 className="card-title"> {offre.adresse}</h5>
            </a>
            <p className="card-text">
            {offre.description}            </p>
            <div>
              <span className="badge badge-primary">{offre.motcle}</span>
              {/* <span className="badge badge-primary">Android</span>
              <span className="badge badge-primary">PHP</span>
              <span className="badge badge-primary">Node.js</span>
              <span className="badge badge-primary">Ruby</span>
              <span className="badge badge-primary">Paython</span> */}
            </div>
          </div>
          <div className="card-footer">
            {/* <a href="#" className="card-link"><i className="fa fa-gittip" /> Like</a>
            <a href="#" className="card-link"><i className="fa fa-comment" /> Comment</a>
            <a href="#" className="card-link"><i className="fa fa-mail-forward" /> Share</a> */}
          </div>
        </div>
                          ))}
                          </div>
      </div>
     
    </div>
  </div>
</main>



        )
    }
}
