import React, { Component } from 'react';
import 'whatwg-fetch';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import AuthService from "../../services/Authentification/authservices";

const required = value => {
	if (!value) {
	  return (
		<div className="alert alert-danger" role="alert">
		  This field is required!
		</div>
	  );
	}
  };


class ResetPassword extends Component {

	constructor(props) {
		super(props);
		this.handleresetpassword = this.handleresetpassword.bind(this);
		this.onChangetoken = this.onChangetoken.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangeConfirmPassword = this.onChangeConfirmPassword.bind(this);



	
		this.state = {
      token:"",
		  password: "",
      passwordConfirm:"",
		  loading: false,
		  message: ""
		};
	  }
	
	  onChangetoken(e) {
		this.setState({
		  token: e
		});
	  }
	
	  onChangePassword(e) {
      this.setState({
        password: e.target.value
      });
    }

    onChangeConfirmPassword(e) {
      this.setState({
          passwordConfirm: e.target.value
      });
    }
	
	  handleresetpassword(e) {
        e.preventDefault();
      this.onChangetoken(new URLSearchParams(this.props.location.search).get("token"))
console.log(new URLSearchParams(this.props.location.search).get("token"));
    
        this.setState({
          message: "",
          successful: false
        });
    
        this.form.validateAll();
    
        if (this.checkBtn.context._errors.length === 0) {
          AuthService.resetpassword(
            new URLSearchParams(this.props.location.search).get("token"),
            this.state.password,

            
          ).then(
            response => {
              this.setState({
                message: response.data.message,
                successful: true
              });
            },
            error => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
    
              this.setState({
                successful: false,
                message: resMessage
              });
            }
          );
        }
      }

    render() {
        return (
          <div class="container">
            <ol class="breadcrumb">
              <li>
                <a href="index.html">Home</a>
              </li>
              <li class="active">Forgot Password</li>
            </ol>

            <div class="row">
              {/* <header class="page-header">
					<h4 class="page-title">Sign in</h4>
				</header>	 */}

              <div class="col-md-6 col-md-offset-3 col-sm-8 col-sm-offset-2">
                <div class="panel panel-default">
                  <div class="panel-body">
                    <h3 class="thin text-center">
                      {" "}
                      Rinitialiser mot de passe
                    </h3>
                    <hr />

                    <Form
                      onSubmit={this.handleresetpassword}
                      ref={(c) => {
                        this.form = c;
                      }}
                    >
                      
                      <Input
                        type="hidden"
                        name="token"
                        value={this.state.token}
                      />
                      <div class="top-margin">
                        <Input
                          type="password"
                          name="password"
                          id="password"
                          className="form-control"
                          placeholder="Enter your new password"
                          value={this.state.password}
                          onChange={this.onChangePassword}
                          validations={[required]}
                        />
                      </div>
                      <div class="top-margin">
                        <Input
                          type="password"
                          className="form-control"
                          placeholder="Confirm your new password"
                          value={this.state.passwordConfirm}
                          onChange={this.onChangeConfirmPassword}
                          validations={[required]}
                        />
                      </div>
                      <hr />
                      <div class="row">
                        <div class="col-lg-4 text-right">
                          <button
                            className="btn btn-action"
                            type="submit"
                            disabled={this.state.loading}
                          >
                            {this.state.loading && (
                              <span className="spinner-border spinner-border-sm"></span>
                            )}
                            Change Password
                          </button>
                        </div>
                      </div>
                      {this.state.message && (
                        <div className="form-group">
                          <div className="alert alert-danger" role="alert">
                            {this.state.message}
                          </div>
                        </div>
                      )}
                      {this.state.message && (
                        <div className="form-group">
                          <div
                            className={
                              this.state.successful
                                ? "alert alert-success"
                                : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {this.state.message}
                          </div>
                        </div>
                      )}
                      <CheckButton
                        style={{ display: "none" }}
                        ref={(c) => {
                          this.checkBtn = c;
                        }}
                      />
                    </Form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
    }

}
    
    

export default ResetPassword;