import axios from "axios";
import { API_BASE_URL, ACCESS_TOKEN } from "../../config/constants";


const API_URL = "http://localhost:8084/";




class AuthService {
  login(username, password) {
    return axios
      .post(API_URL + "signin", {
        username,
        password,
      })
      .then((response) => {
        if (response.data.accessToken) {
          localStorage.setItem("user", JSON.stringify(response.data));
        }

        return response.data;
      });
  }

  logout() {
    localStorage.removeItem("accessToken");
    localStorage.removeItem("token");
    localStorage.removeItem("ROLE_ENTREPRISE");
    localStorage.removeItem("user");
    localStorage.removeItem("ROLE_USER");


  }

  register(
    username,
    email,
    password,
    passwordConfirm,
    nom,
    prenom,
    address,
    dateN,
    tel,
    sexe,
    roles,
    file,
  ) {
    const candidat ={
      username,
      email,
      password,
      passwordConfirm,
      nom,
      prenom,
      address,
      dateN,
      tel,
      sexe,
      

    };
    let formData = new FormData();
        formData.append("candidat", JSON.stringify(candidat));
        formData.append("file", file);
    return axios.post(API_URL + "signup", formData);
  }
  client(
    
    username,
    email,
    password,
    passwordConfirm,
    nom,
    prenom,
    entreprise,
    address,
    tel,
    descripssion,
    secteur,
    roles,
    file
  ) {
    const client = {   username,
      email,
      password,
      passwordConfirm,
      nom,
      prenom,
      entreprise,
      address,
      tel,
      descripssion,
      secteur,
      };
      let formData = new FormData();
        formData.append("client", JSON.stringify(client));
        formData.append("file", file);
    return axios.post(API_URL + "entreprise", formData);
  }
  getEmployeeById(id) {
    return axios.get(API_URL + "user/" + id);
  }

  update(id, data) {
    return axios.put(API_URL + `update-profile/${id}`, data);
  }

  updateCandidature(id, nom, prenom, address, dateN, tel, email, file) {
    let formData = new FormData();
    formData.append("nom", nom);
    formData.append("prenom", prenom);
    formData.append("address", address);
    formData.append("dateN", dateN);
    formData.append("tel", tel);
    formData.append("email", email);
    formData.append("file", file);
    return axios.put(API_URL + `update-profile/${id}`, formData);
  }

  forgotpassword(email) {
    return axios.post(API_URL + "forgot_password/" + email, {
      email,
    });
  }

  resetpassword(token, password) {
    return axios.post(API_URL + "reset_password/", {
      token,
      password,
    });
  }

  getCurrentUser() {
    return JSON.parse(localStorage.getItem("user"));
  }

  
  
}



export default new AuthService();